import { Button, Typography } from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { NavLink, useNavigate } from "react-router-dom";

import { setGetSouscription } from "../../../features/user.reducers";

const Souscription = () => {
  const user = useSelector((state) => state.user.user);
  const dispatch = useDispatch();
  const { sucees, setSucess } = useState("");
  const [error, setError] = useState("");
  const history = useNavigate();
  async function handleSouscription() {
    const res = await axios
      .post(`${process.env.REACT_APP_URL_USER}/souscription/${user._id}`)
      .then((res) => {
        setSucess(res.data.message);
      })
      .catch((error) => {
        setError(error.response.data.message);
      });
    const data = await res.data;
    return data;
  }
  useEffect(() => {
    handleSouscription().then((data) => {
      dispatch(setGetSouscription(data));
      history("/home");
    });
  });
  return (
    <div>
      <NavLink to="/home">Retour à l'accueil</NavLink>
      <>
        {user.souscription === "1" ? (
          <>
            <Typography variant="subtitle2">{sucees}</Typography>
            <Button>
              <a
                href={`../../souscription/${user._id}_souscription.pdf`}
                target="blank"
                download
              >
                Télécharger votre borderau de souscription
              </a>
            </Button>
          </>
        ) : (
          <>
            <Typography variant="subtitle2">{error}</Typography>
          </>
        )}
      </>
    </div>
  );
};

export default Souscription;
