import axios from "axios";
import { useDispatch } from "react-redux";
import * as React from "react";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { addvideo } from "../../../features/video.reducers";
import { Stack } from "react-bootstrap";
import { Alert, CircularProgress, Snackbar } from "@mui/material";

const theme = createTheme();

export default function Video() {
  /**State local */

  const [titles, setTitles] = React.useState("");

  const [description, setdescription] = React.useState("");

  const [file, setFile] = React.useState();

  const [error, setError] = React.useState("");

  const dispatch = useDispatch();

  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  /**Function pour créer nos partitions */
  const handlePostUpload = async () => {
    const data = new FormData();
    data.append("title", titles);

    data.append("description", description);
    data.append("video", file);

    const res = await axios.post(
      `${process.env.REACT_APP_URL_VIDEO}/create`,
      data
    );

    const datas = await res.data;
    return datas;
  };
  const handlePost = async (e) => {
    e.preventDefault();
    setLoading(true);
    handlePostUpload()
      .then((datas) => {
        // setResponse(true)
        dispatch(addvideo(datas));
        setLoading(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(datas.message);
      })
      .catch((error) => {
        setLoading(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };

  return (
    <>
      {
        <ThemeProvider theme={theme}>
          {resToHome ? (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="success"
                  sx={{ width: "100%" }}
                >
                  {succees}
                </Alert>
              </Snackbar>
            </Stack>
          ) : (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="error"
                  sx={{ width: "100%" }}
                >
                  {error}
                </Alert>
              </Snackbar>
            </Stack>
          )}

          <Container component="main">
            <CssBaseline />
            <Box
              sx={{
                marginTop: 8,
                display: "flex",
                flexDirection: "column",

                width: "100%",
              }}
            >
              <Typography align="center" component="h1" variant="h5">
                Les vidéos
              </Typography>
              <form onSubmit={handlePost}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      required
                      id="title"
                      label="Title"
                      name="title"
                      onChange={(e) => setTitles(e.target.value)}
                      value={titles}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      multiline
                      rows={4}
                      id="description"
                      label="description"
                      name="description"
                      value={description}
                      onChange={(e) => {
                        setdescription(e.target.value);
                      }}
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      name="file"
                      onChange={(e) => {
                        setFile(e.target.files[0]);
                      }}
                      type="file"
                      id="file"
                      accept=".jpg, .jpeg, .png"
                    />
                  </Grid>
                </Grid>
                {loading ? (
                  <CircularProgress />
                ) : (
                  <Button
                    type="submit"
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                  >
                    Envoyer
                  </Button>
                )}
              </form>
            </Box>
          </Container>
        </ThemeProvider>
      }
    </>
  );
}
