import React from "react";
import {
  Button,
  Card,
  CardContent,
  Grid,
  Typography,
  styled,
} from "@mui/material";

import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import axios from "axios";
import { useSelector } from "react-redux";

const PostCard = styled(Card)(({ theme }) => ({}));

const CardContentWrapper = styled(CardContent)(({ theme }) => ({
  paddingBottom: `${theme.spacing(2)} !important`,
}));

function Post({ exercice }) {
  const user = useSelector((state) => state.user.user);
  const handleDelete = async () => {
    axios.delete(`${process.env.REACT_APP_URL_EXERCICES}/${exercice._id}`);
  };

  return (
    <PostCard
      style={{
        width: "100%",
        backgroundImage: `url('logo.jpg')`,
        backgroundPosition: "center",
        backgroundSize: "cover",
        color: "white",
      }}
    >
      <CardContentWrapper>
        <Grid container alignItems="center">
          <Grid item xs>
            <Typography variant="body1" component="div">
              <span
                style={{
                  color: "gray",
                  marginLeft: "10px",
                  fontSize: "1.1rem",
                }}
              >
                Prophète Dalum Samuel
                <CheckCircleIcon style={{ fontSize: 17, color: "blue" }} />
              </span>
              <br />
              {/* <span style={{ color: 'white',position:'relative',right:'0' }}>Annonce du {exercice.date}   </span> */}
            </Typography>

            <Typography
              style={{ textAlign: "justify" }}
              variant="body1"
              color="white"
            >
              {exercice.message}
            </Typography>
          </Grid>
        </Grid>
      </CardContentWrapper>
      {user.email === process.env.REACT_APP_URL_ADMINPRINCIPAL && (
        <Button
          color="error"
          variant="contained"
          onClick={() => {
            if (window.confirm("Voulez vous vraiment supprimez ?")) {
              // eslint-disable-next-line no-lone-blocks
              {
                handleDelete();
              }
            }
          }}
        >
          Supprimer{" "}
        </Button>
      )}
      <br />
      <br />
    </PostCard>
    //  <Grid container spacing={2}>
    //   {exercice.picture.map((image) => (
    //     <Grid item xs={6} md={4} lg={3} key={image.id}>
    //       <Card >
    //         <CardMedia
    //           component="img"

    //           image={image}
    //           alt="exercices"
    //         />
    //       </Card>
    //         <Typography>{exercice.message}</Typography>
    //     </Grid>
    //   ))}
    // </Grid>
  );
}

export default Post;
