import axios from "axios";
import { useDispatch } from "react-redux";
import * as React from "react";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { addImages } from "../../../features/images.reducers";
import {
  Alert,
  CircularProgress,
  InputAdornment,
  MenuItem,
  Snackbar,
  Stack,
} from "@mui/material";
import AudiotrackIcon from "@mui/icons-material/Audiotrack";

const theme = createTheme();

export default function Image() {
  /**State local */
  const [titles, setTitles] = React.useState("");
  const [category, setCategory] = React.useState("");
  const [partition, setpartition] = React.useState("");
  const [file, setFile] = React.useState();
  const [error, setError] = React.useState("");

  const dispatch = useDispatch();
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const currencies = [
    {
      value: "All",
      label: "All",
    },
    {
      value: "Soprano",
      label: "Soprano",
    },
    {
      value: "Alto",
      label: "Alto",
    },
    {
      value: "Ténor",
      label: "Ténor",
    },
    {
      value: "Basse",
      label: "Basse",
    },
    {
      value: "Percussion",
      label: "Percussion",
    },
  ];

  /**Function pour créer nos partitions */
  const handlePostUpload = async () => {
    const data = new FormData();
    data.append("titre", titles);
    data.append("category", category);
    data.append("partition", partition);
    data.append("picture", file);

    const res = await axios.post(
      `${process.env.REACT_APP_URL_PICTURE}/create`,
      data
    );

    const datas = await res.data;
    return datas;
  };
  const handlePost = async (e) => {
    e.preventDefault();
    setLoading(true);
    handlePostUpload()
      .then((datas) => {
        dispatch(addImages(datas));
        setLoading(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(datas.message);
      })
      .catch((error) => {
        setLoading(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };

  return (
    <>
      {resToHome ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
      {
        <ThemeProvider theme={theme}>
          <Container component="main">
            <CssBaseline />
            <Box
              sx={{
                marginTop: 8,
                display: "flex",
                flexDirection: "column",

                width: "100%",
              }}
            >
              <Typography align="center" component="h1" variant="h5">
                Envoyer une Image
              </Typography>
              <form onSubmit={handlePost}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      required
                      id="titre"
                      label="Titre"
                      name="title"
                      onChange={(e) => setTitles(e.target.value)}
                      value={titles}
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="category"
                      label="category"
                      name="category"
                      value={category}
                      onChange={(e) => {
                        setCategory(e.target.value);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      name="file"
                      onChange={(e) => {
                        setFile(e.target.files[0]);
                      }}
                      type="file"
                      id="file"
                      accept=".jpg, .jpeg, .png"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      margin="normal"
                      required
                      id="partitions"
                      select
                      label="Partition"
                      defaultValue=""
                      name="partition"
                      onChange={(e) => setpartition(e.target.value)}
                      value={partition}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <AudiotrackIcon />
                          </InputAdornment>
                        ),
                      }}
                    >
                      {currencies.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </TextField>
                  </Grid>
                </Grid>
                {loading ? (
                  <CircularProgress />
                ) : (
                  <Button
                    type="submit"
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                  >
                    Envoyer
                  </Button>
                )}
              </form>
            </Box>
          </Container>
        </ThemeProvider>
      }
    </>
  );
}
