import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";

import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import CircularProgress from "@mui/material/CircularProgress";
import VisibilityIcon from "@mui/icons-material/Visibility";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import PasswordIcon from "@mui/icons-material/Password";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import MarkEmailReadIcon from "@mui/icons-material/MarkEmailRead";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import PersonIcon from "@mui/icons-material/Person";
import AudiotrackIcon from "@mui/icons-material/Audiotrack";
import PianoIcon from "@mui/icons-material/Piano";
import HealthAndSafetyIcon from "@mui/icons-material/HealthAndSafety";
import axios from "axios";
import {
  Alert,
  FormControl,
  FormControlLabel,
  InputAdornment,
  MenuItem,
  Radio,
  RadioGroup,
  Snackbar,
  Stack,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import Visibility from "@mui/icons-material/Visibility";

const theme = createTheme();
export default function SignUpSide() {
  const [error, setError] = React.useState("");
  const [succees, setsuccees] = React.useState("");
  const [showPassword, setShowPassword] = React.useState(false);

  const history = useNavigate();
  const [open, setOpen] = React.useState(false);
  const [resToSignIn, setResToSignIn] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [value, setValue] = React.useState("");

  const handleChangeSexe = (event) => {
    setValue(event.target.value);
  };

  const currencies = [
    {
      value: "Soprano",
      label: "Soprano",
    },
    {
      value: "Alto",
      label: "Alto",
    },
    {
      value: "Ténor",
      label: "Ténor",
    },
    {
      value: "Basse",
      label: "Basse",
    },
    {
      value: "Percussion",
      label: "Percussion",
    },
  ];
  const currenciesGroupe = [
    {
      value: "A+",
      label: "A+",
    },
    {
      value: "A-",
      label: "A-",
    },
    {
      value: "B+",
      label: "B+",
    },
    {
      value: "B-",
      label: "B-",
    },
    {
      value: "AB+",
      label: "AB+",
    },
    {
      value: "AB-",
      label: "AB-",
    },
    {
      value: "O+",
      label: "O+",
    },
    {
      value: "O-",
      label: "O-",
    },
    {
      value: "",
      label: "Je ne connais pas",
    },
  ];
  const currenciesInstrument = [
    {
      value: "Batterie",
      label: "Batterie",
    },
    {
      value: "Baryton",
      label: "Baryton",
    },
    {
      value: "Contrebasse",
      label: "Contrebasse",
    },
    {
      value: "Cornet",
      label: "Cornet",
    },
    {
      value: "Cor d'harmonie",
      label: "Cor d'harmonie",
    },
    {
      value: "Caisse claire",
      label: "Caisse claire",
    },
    {
      value: "Clarinette",
      label: "Clarinette",
    },
    {
      value: "Cymbale",
      label: "Cymbale",
    },
    {
      value: "Euphonium",
      label: "Euphonium",
    },
    {
      value: "Flûte traversière",
      label: "Flûte traversière",
    },
    {
      value: "Flûte à bec",
      label: "Flûte à bec",
    },
    {
      value: "Gong",
      label: "Gong",
    },
    {
      value: "Sylophone",
      label: "Sylophone",
    },
    {
      value: "Saxophone",
      label: "Saxophone",
    },

    {
      value: "Trompette",
      label: "Trompette",
    },
    {
      value: "Tambour",
      label: "Tambour",
    },
    {
      value: "Trombone",
      label: "Trombone",
    },
    {
      value: "Tuba",
      label: "Tuba",
    },
    {
      value: "Violon",
      label: "Violon",
    },

    {
      value: "Violoncelle",
      label: "Violoncelle",
    },
  ];

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);
    if (!navigator.onLine) {
      setLoading(false);
      setOpen(true);
      return setError(
        "Si la connexion n'est pas active, vérifiez la connexion"
      );
    }

    // else if (!acceptTermsValue) {
    //   setLoading(false)
    //     setOpen(true)
    //   setError("Remplissez touts les champs")
    //   }
    else {
      const data = new FormData(event.currentTarget);
      axios
        .post(`${process.env.REACT_APP_URL_USER}/register`, {
          firstName: data.get("firstName"),
          lastName: data.get("lastName"),
          email: data.get("email"),
          tel: data.get("tel"),
          password: data.get("password"),
          partition: data.get("partitions"),
          instrument: data.get("instrument"),
          sanguin: data.get("sanguin"),
          sexe: value,
        })
        .then((res) => {
          setLoading(false);
          // console.log(res.data.message);
          setResToSignIn(true);
          setOpen(true);
          setsuccees(res.data.message);

          setTimeout(() => {
            history("/login");
          }, 5000);
        })
        .catch((error) => {
          setLoading(false);
          setOpen(true);
          if (error.response.data.message)
            return setError(error.response.data.message);
          else setError("Veuillez vérifez votre connexion");
        });
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: "100vh" }}>
        <CssBaseline />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "blue" }}>
              {/* <LockOutlinedIcon /> */}
            </Avatar>
            <Typography component="h1" variant="h6" textTransform="uppercase">
              Inscription
            </Typography>
            <Box
              component="form"
              autoComplete=""
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              <TextField
                margin="normal"
                required
                fullWidth
                id="firstName"
                label="Nom de famille"
                name="firstName"
                autoComplete="firstName"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="lastName"
                label="Prenom"
                name="lastName"
                autoComplete="lastName"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email"
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <MarkEmailReadIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="tel"
                label="Numéro de téléphone"
                name="tel"
                autoComplete="tel"
                inputProps={{ maxLength: 8 }}
                autoFocus
                helperText="Mettez un numéro de téléphone sans l'indicative du pays: Ex:50000000"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <LocalPhoneIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                fullWidth
                margin="normal"
                required
                id="partitions"
                select
                label="Partition"
                defaultValue=""
                name="partitions"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AudiotrackIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {currencies.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>
              <TextField
                fullWidth
                margin="normal"
                id="instrument"
                select
                label="Instrument"
                defaultValue=""
                required
                name="instrument"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PianoIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {currenciesInstrument.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                fullWidth
                margin="normal"
                id="sanguin"
                select
                label="Groupe sanguin"
                defaultValue=""
                helperText="Le choix du groupe sanguin est facultatif"
                name="sanguin"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <HealthAndSafetyIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {currenciesGroupe.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="Mot de passe"
                // type="password"
                id="password"
                autoComplete="current-password"
                endIcon={<VisibilityIcon />}
                type={showPassword ? "text" : "password"}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PasswordIcon />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                      sx={{ cursor: "pointer" }}
                      position="end"
                    >
                      {" "}
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </InputAdornment>
                  ),
                }}
              />

              <FormControl>
                {/* <FormLabel variant='h1'  id="sexe">Sexe</FormLabel> */}

                <Typography
                  variant="subtitle2"
                  marginLeft="12px"
                  fontSize="12px"
                >
                  Sexe
                </Typography>
                <RadioGroup
                  aria-labelledby="sexe"
                  name="sexe"
                  // defaultValue="Masculin"
                  value={value}
                  required
                  onChange={handleChangeSexe}
                  sx={{ display: "flex", flexDirection: "row" }}
                >
                  <FormControlLabel
                    size="small"
                    value="Masculin"
                    control={<Radio />}
                    label="Masculin"
                  />
                  <FormControlLabel
                    size="small"
                    value="Féminim"
                    control={<Radio />}
                    label="Feminin"
                  />
                </RadioGroup>
              </FormControl>
              <FormControl
                sx={{ m: 1, width: "25ch" }}
                variant="filled"
              ></FormControl>

              {/* <Box>
                <Box>
                  <FormControlLabel label="j'accepte les conditions d'utulisation" control={<Checkbox checked={acceptTermsValue} color="secondary" onChange={(e) => setAacceptTermsValue(e.target.checked)} />} >
                  </FormControlLabel>
                 
                </Box>
             </Box> */}
              {loading ? (
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <CircularProgress />
                </Box>
              ) : (
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{
                    mt: 3,
                    mb: 2,
                    background: {
                      md: "#FBB437",
                      xs: "#FBB437",
                      "&:hover": {
                        background: "#6D4826",
                      },
                    },
                  }}
                >
                  Je m'inscire
                </Button>
              )}

              <Grid container>
                <Grid item>
                  <Button
                    variant="text"
                    sx={{ textTransform: "capitalize", fontSize: "1rem" }}
                    size="small"
                    onClick={() => {
                      history("/login");
                    }}
                  >
                    {"Se connecter"}
                  </Button>
                  <Button
                    variant="text"
                    sx={{ textTransform: "capitalize", fontSize: "1rem" }}
                    size="small"
                    onClick={() => {
                      history("/politique");
                    }}
                  >
                    Politique de confidentialité
                  </Button>
                  {/* <Button sx={{ textTransform: 'capitalize',textDecoration:'underline' }} onClick={() => {
                    


                    history('/condition')
                  }}>

                  Condition d'utilisation
                  </Button> */}
                </Grid>
              </Grid>
            </Box>
          </Box>
        </Grid>
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            // backgroundColor:"#FBB437",

            backgroundImage: `url('logo.jpg')`,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            backgroundSize: "cover",
            objectFit: "cover",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
          }}
        />
      </Grid>
      {resToSignIn ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
    </ThemeProvider>
  );
}
