import {
  Button,
  Card,
  CardContent,
  CardMedia,
  Typography,
} from "@mui/material";

import { styled } from "@mui/material/styles";
import { useSelector } from "react-redux";

import axios from "axios";

const AudioPlayerWrapper = styled(Card)({
  maxWidth: 600,
  margin: "auto",
});

const AudioPlayerImage = styled(CardMedia)({
  height: 130,
});

const AudioPlayerControls = styled("div")({
  display: "flex",
  alignItems: "center",
});

const Music = ({ audio }) => {
  const user = useSelector((state) => state.user.user);

  const handleDelete = async () => {
    axios
      .delete(`${process.env.REACT_APP_URL_AUDIO}/delete_audio/${audio._id}`)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => alert(err.response.data.message));
  };

  return (
    <div>
      <AudioPlayerWrapper
        sx={{ display: "flex", justifyContent: "space-around" }}
        key={audio._id}
        variant="outlined"
      >
        <AudioPlayerImage component="img" image="./logo.jpg" />

        <CardContent>
          <Typography variant="h6" component="h2">
            {audio.title}
          </Typography>
          <Typography color="textSecondary">LGP</Typography>
          <AudioPlayerControls></AudioPlayerControls>
          <audio controls>
            <source src={audio.audio} type="audio/ogg"></source>
            <source src={audio.audio} type="audio/mpeg"></source>
            Votre navigateur ne supporte pas la lecture de fichiers audio.
          </audio>
        </CardContent>
      </AudioPlayerWrapper>
      {user.isAdminDev && (
        <Button
          color="error"
          variant="contained"
          onClick={() => {
            if (window.confirm("Voulez vous vraiment supprimez ?")) {
              // eslint-disable-next-line no-lone-blocks
              {
                handleDelete();
              }
            }
          }}
        >
          Supprimer{" "}
        </Button>
      )}
      <br /> <br />
    </div>
  );
};

export default Music;
