import { createSlice } from "@reduxjs/toolkit";

export const calendarslice = createSlice({
  name: "calendars",
  initialState: {
    calendars: [],
  },
  reducers: {
    getAllPost: (state, { payload }) => {
      state.calendars = payload;
    },

    addPost: (state, { payload }) => {
      state.calendars?.push(payload);
    },

    deletePost: (state, { payload }) => {
      state.calendars = state.calendars.filter((post) => post._id !== payload);
    },
    // deleteUser: (state, { payload }) => {
    //   state.user = state.user.filter((user) => {
    //     return user.id !== payload;
    //   });
    // },
    likePost: (state, { payload }) => {
      state.calendars = state.calendars?.map((post) => {
        if (post._id === payload) {
          return state.calendars.likers?.push(payload);
        }
        return post;
      });
    },

    dislikePost: (state, { payload }) => {
      state.calendars = state.calendars?.map((post) => {
        if (post._id === payload) {
          return state.calendars.likers?.filter((id) => id !== payload);
        }
        return post;
      });
    },

    editPost: (state, { payload }) => {
      state.calendars = state.calendars.map((post) => {
        if (post._id === payload) {
          return state.calendars.message?.push(payload);
        } else return post;
      });
    },
    // calendars commentaires
  },
});

export const {
  getAllPost,

  addPost,
  deletePost,
  likePost,
  dislikePost,
} = calendarslice.actions;
export default calendarslice.reducer;
