import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";

import { dislikePost, likePost } from "../../../features/calendar.reducers";
import { Button, Typography } from "@mui/material";

const LikeButon = ({ selectedEvent }) => {
  const dispatch = useDispatch();
  const [liked, setLiked] = useState(false);
  const user = useSelector((state) => state.user.user);

  const [countdown, setCountdown] = useState("");
  const [endTime, setEndTime] = useState(true);
  // const uid = user._id

  const sendRequestLike = async () => {
    const res = await axios
      .patch(
        `${process.env.REACT_APP_URL_CALENDAR}/like-post/${selectedEvent._id}`,

        {
          id: user._id,
        }
      )
      .catch((error) => {
        console.log(error);
      });

    const data = await res.data;
    return data;
  };
  const sendRquestPostunLike = async () => {
    const res = await axios
      .patch(
        `${process.env.REACT_APP_URL_CALENDAR}/unlike-post/${selectedEvent._id}`,

        {
          id: user._id,
        }
      )
      .catch((error) => {
        console.log(error);
      });

    const data = await res.data;
    return data;
  };
  const like = () => {
    sendRequestLike().then((data) => {
      dispatch(likePost(data));
      setLiked(true);
    });
  };
  const unlike = () => {
    sendRquestPostunLike().then(() => {
      dispatch(dislikePost());
      setLiked(false);
    });
  };

  useEffect(() => {
    if (selectedEvent.likers?.includes(user._id)) return setLiked(true);
    else return setLiked(false);
  }, [user, selectedEvent]);

  useEffect(() => {
    const startDate = new Date(selectedEvent.start);
    // const endDate = new Date(startDate.getTime() - 259200000)
    const endDate = new Date(startDate.getTime());
    const intervalId = setInterval(() => {
      const currentDate = new Date();
      const timeDiff = endDate.getTime() - currentDate.getTime();
      if (timeDiff < 0) {
        clearInterval(intervalId);
        setCountdown("Le compte à rebours est terminé !");
        setEndTime(true);
      } else {
        const daysRemaining = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
        const hoursRemaining = Math.floor(
          (timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        const minutesRemaining = Math.floor(
          (timeDiff % (1000 * 60 * 60)) / (1000 * 60)
        );
        const secondsRemaining = Math.floor((timeDiff % (1000 * 60)) / 1000);
        setEndTime(false);

        const countdownStr =
          ("0" + daysRemaining).slice(-2) +
          "j " +
          ("0" + hoursRemaining).slice(-2) +
          "h " +
          ("0" + minutesRemaining).slice(-2) +
          "m " +
          ("0" + secondsRemaining).slice(-2) +
          "s";

        setCountdown(countdownStr);
      }
    }, 1000);

    return () => clearInterval(intervalId);
  }, [selectedEvent]);

  return (
    <>
      <Typography> Cloture de la liste : {countdown}</Typography>
      {!endTime ? (
        <>
          {liked === false && (
            <>
              <Button
                style={{ color: "black", cursor: "pointer" }}
                variant="contained"
                size="small"
                onClick={like}
              >
                Je participe à l'évènement
              </Button>
              <Typography variant="subtitle2"></Typography>
            </>
          )}
          {liked && (
            <>
              {/* <i style={{ color: 'blue', cursor: 'pointer' }} onClick={unlike} className="fa fa-thumbs-up fa-fw fa-lg m-r-3"></i> */}
              <Button
                variant="filled"
                onClick={unlike}
                style={{ color: "blue", cursor: "pointer" }}
              >
                Je ne participe plus
              </Button>
            </>
          )}
        </>
      ) : (
        ""
      )}
    </>
  );
};

export default LikeButon;
