import {
  Alert,
  Box,
  Button,
  Container,
  Icon,
  Paper,
  Snackbar,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import axios from "axios";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setGetUser, updateProfil } from "../../features/user.reducers";
import CloseIcon from "@mui/icons-material/Close";
import { NavLink, useNavigate } from "react-router-dom";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

const Profil = () => {
  const user = useSelector((state) => state.user.user);
  const dispatch = useDispatch();
  const [nom, setnom] = React.useState(user.firstName);
  const [prenom, setprenom] = React.useState(user.lastName);
  const [tel, setTel] = React.useState(user.tel);
  const [email, setEmail] = React.useState(user.email);
  const [updateForm, setUpadateForm] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [resToSignIn, setResToSignIn] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [loading, setLoading] = React.useState(false);
  const history = useNavigate();

  const [error, setError] = React.useState("");
  const EditProfilRequest = async () => {
    const res = await axios
      .put(
        `${process.env.REACT_APP_URL_USER}/update/${user._id}`,

        {
          firstName: nom,
          tel: tel,
          lastName: prenom,
          email: email,
        }
      )
      .then((res) => {
        //  console.log(res.data.message);
        setLoading(false);
        setOpen(true);
        setsuccees(res.data.message);
        setResToSignIn(true);
        //  setUpadateForm(false)
        setTimeout(() => {
          history("/home");
        }, 3000);
        axios.get(`${process.env.REACT_APP_URL_USER}/jwt`);
      })
      .catch((error) => {
        setLoading(false);
        setOpen(true);
        setError(error.response.data.message);
      });
    const datas = await res.data;
    return datas;
  };
  const handleUpdate = (e) => {
    e.preventDefault();

    setLoading(false);

    EditProfilRequest().then((datas) => {
      dispatch(updateProfil(datas));
    });
  };

  const getUserWithVerifyToken = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  useEffect(() => {
    getUserWithVerifyToken()
      .then((data) => {
        dispatch(setGetUser(data.user));
        // console.log(data);
      })
      .catch((error) => {
        setTimeout(() => {
          // console.log(error);
          history("/login");
        }, 2000);
      });
  }, [dispatch]);

  return (
    <div
      style={{
        backgroundImage: `url('./assets/fond2.jpg')`,
        backgroundPosition: "center",
        backgroundSize: "cover",
        height: "100vh",
        backgroundColor:
          "rgba(128, 128, 128, 0.5)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
      }}
    >
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>

      {/* <Typography
          variant='h5' sx={{ textAlign: "center" }}>
                Profil
        </Typography>
        <br /> */}
      <>
        {/* {
             user.souscription ? <Typography variant='subtitle2'>Compte actif</Typography> :
              <>
                

                <Typography variant='subtitle2'>Compte inactif</Typography>
              <Button size='small' variant='contained'> <a
                  style={{ textDecoration: "none" }}
                  href="https://me.fedapay.com/souscription_1234"
                  target="blank"
                >
                  Souscrire
                </a></Button>
          </>
        } */}
      </>
      <Typography variant="h5" sx={{ textAlign: "center" }}>
        Mon compte
      </Typography>
      <Typography variant="subtitle1">
        NB : Si c'est la partition ou l'instrument qui est mal choisi, veuillez
        notifier aux admins qui supprimeront ce profil, alors vous pouvez créer
        un autre compte
      </Typography>
      <br />
      {updateForm === false && (
        <Stack spacing={3} direction="column">
          <Paper sx={{ height: "40px" }} elevation={3}>
            <Typography
              variant="subtitle2"
              sx={{ transform: "translateY(70%)" }}
            >
              Nom: {user.firstName}
            </Typography>
          </Paper>

          <Paper sx={{ height: "40px" }} elevation={3}>
            <Typography
              variant="subtitle2"
              sx={{ transform: "translateY(70%)" }}
            >
              Prénom: {user.lastName}
            </Typography>
          </Paper>
          <Paper sx={{ height: "40px" }} elevation={3}>
            <Typography
              variant="subtitle2"
              sx={{ transform: "translateY(70%)" }}
            >
              Instrument: {user.instrument}
            </Typography>
          </Paper>

          <Paper sx={{ height: "40px" }} elevation={3}>
            <Typography
              variant="subtitle2"
              sx={{ transform: "translateY(70%)" }}
            >
              Partition :{user.partition}
            </Typography>
          </Paper>
          {user.sanguin !== "" && (
            <Paper sx={{ height: "40px" }} elevation={3}>
              <Typography
                variant="subtitle2"
                sx={{ transform: "translateY(70%)" }}
              >
                Groupe sanguin: {user.sanguin}
              </Typography>
            </Paper>
          )}
          <Paper sx={{ height: "40px" }} elevation={3}>
            <Typography
              variant="subtitle2"
              sx={{ transform: "translateY(70%)" }}
            >
              Email: {user.email}
            </Typography>
          </Paper>

          <Paper sx={{ height: "40px" }} elevation={3}>
            <Typography
              variant="subtitle2"
              sx={{ transform: "translateY(70%)" }}
            >
              Tél: {user.tel}
            </Typography>
          </Paper>
          <Button
            type="submit"
            sx={{ marginTop: 3, borderRadius: 1 }}
            variant="contained"
            onClick={() => setUpadateForm(!updateForm)}
          >
            Modifier mon profil
          </Button>
        </Stack>
      )}

      {updateForm && (
        <Container component="main" maxWidth="xs">
          <Box
            sx={{
              marginTop: 8,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Box
              component="form"
              onSubmit={handleUpdate}
              noValidate
              sx={{ mt: 1, position: "absolute" }}
            >
              <Icon>
                <CloseIcon
                  onClick={() => setUpadateForm(!updateForm)}
                  sx={{
                    position: "absolute",
                    right: "0",
                    top: "0",
                    cursor: "pointer",
                    fontSize: "2rem",
                  }}
                />
              </Icon>
              <TextField
                required
                label="Nom"
                fullWidth
                margin="normal"
                type={"text"}
                variant="outlined"
                placeholder="Votre nouveau nom"
                value={nom}
                onChange={(e) => setnom(e.target.value)}
              />
              <TextField
                required
                fullWidth
                label="Prenom"
                margin="normal"
                type={"text"}
                variant="outlined"
                placeholder="Votre nouveau prenom"
                value={prenom}
                onChange={(e) => setprenom(e.target.value)}
              />
              <TextField
                required
                fullWidth
                label="Email"
                margin="normal"
                type={"email"}
                variant="outlined"
                placeholder="Votre nouveau email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              <TextField
                required
                fullWidth
                label="tel"
                margin="normal"
                type={"text"}
                variant="outlined"
                placeholder="Votre nouveau numéro de tel"
                value={tel}
                onChange={(e) => setTel(e.target.value)}
              />
              <Typography variant="subtitle2">
                Ne touchez pas le champ email si vous vouliez pas le modifier
              </Typography>
              <Stack direction="row">
                {loading ? (
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    disabled
                    sx={{ mt: 3, mb: 2 }}
                  >
                    Loading...
                  </Button>
                ) : (
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                  >
                    Mettre à jour
                  </Button>
                )}
              </Stack>
            </Box>

            <br />
            <br />
            <br />
          </Box>

          {resToSignIn ? (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="success"
                  sx={{ width: "100%" }}
                >
                  {succees}
                </Alert>
              </Snackbar>
            </Stack>
          ) : (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="error"
                  sx={{ width: "100%" }}
                >
                  {error}
                </Alert>
              </Snackbar>
            </Stack>
          )}
        </Container>
      )}
    </div>
  );
};

export default Profil;
