import { Icon, Paper, h4 } from "@mui/material";
import { Stack } from "@mui/system";

import EventAvailableIcon from "@mui/icons-material/EventAvailable";
import { Gavel } from "@mui/icons-material";
import React from "react";
import { NavLink } from "react-router-dom";
import {
  MusicNote,
  PhotoLibrary,
  Note,
  AccountCircle,
} from "@mui/icons-material";

const Card = () => {
  return (
    <div>
      <br />
      <Stack
        direction="row"
        justifyContent="space-evenly"
        flexWrap="nowrap"
        alignItems="center"
        spacing={2}
      >
        <NavLink to="/partition">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#f20c18",
            }}
          >
            <h4
              style={{
                position: "absolute",
                left: "0",
                bottom: "0",
                color: "black",
                margin: "10px",
              }}
              gutterBottom
            >
              Vos portées musicales
            </h4>
            <Icon>
              <MusicNote
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "black",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>

        <NavLink to="/gallery">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#EF7720",
            }}
          >
            <h4
              style={{
                position: "absolute",
                left: "0",
                bottom: "0",
                color: "black",
                margin: "10px",
              }}
              gutterBottom
            >
              Galéries d'images, d'audio et vidéo
            </h4>
            <Icon>
              <PhotoLibrary
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "black",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
      </Stack>
      <br />
      <br />
      <Stack
        direction="row"
        justifyContent="space-evenly"
        flexWrap="nowrap"
        alignItems="center"
        spacing={2}
      >
        <NavLink to="/education">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#109836",
            }}
          >
            <h4
              style={{
                position: "absolute",
                left: "0",
                bottom: "0",
                color: "black",
                margin: "10px",
              }}
              gutterBottom
            >
              Mes notes et communiquées
            </h4>
            <Icon>
              <Note
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "black",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
        <NavLink to="/transaction">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#2FABB5",
            }}
          >
            <h4
              style={{
                position: "absolute",
                left: "0",
                bottom: "0",
                color: "black",
                margin: "10px",
              }}
              gutterBottom
            >
              Calendrier des évènements
            </h4>
            <Icon>
              <EventAvailableIcon
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "black",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
      </Stack>
      <br />
      <br />
      <Stack
        direction="row"
        justifyContent="space-evenly"
        flexWrap="nowrap"
        alignItems="center"
        spacing={2}
      >
        <NavLink to="/notification">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#D8A929",
            }}
          >
            <h4
              style={{
                position: "absolute",
                left: "0",
                bottom: "0",
                color: "black",
                margin: "10px",
              }}
              gutterBottom
            >
              Règlement intérieur du groupe
            </h4>
            <Icon>
              <Gavel
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "black",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
        <NavLink to="/profil">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#40E0D0",
            }}
          >
            <h4
              style={{
                position: "absolute",
                left: "0",
                bottom: "0",
                color: "black",
                margin: "10px",
              }}
              gutterBottom
            >
              Modifié vos informations de profil
            </h4>
            <Icon>
              <AccountCircle
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "black",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
      </Stack>

      <br />
      <br />
    </div>
  );
};

export default Card;
