import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { NavLink } from "react-router-dom";
import { Box, Button, Icon, Typography } from "@mui/material";
import VisibilityIcon from "@mui/icons-material/Visibility";
import { useSelector } from "react-redux";
import CalendarComponent from "./Calendar";

function createData(name, date, montant, statut, recu) {
  return { name, date, montant, statut, recu };
}

const rows = [
  createData("Carte perso", "07/02/2023", "1500", "payé", <VisibilityIcon />),
];

export default function BasicTable() {
  const user = useSelector((state) => state.user?.user);

  return (
    <Box
      sx={{
        backgroundImage: `url('./assets/calendar.jpg')`,
        backgroundPosition: "left",
        // height: "100%",
        width: "100%",
        backgroundSize: "cover",
        backgroundColor:
          "rgba(128, 128, 128, 0.5)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
      }}
    >
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>

      <h2>Transactions</h2>
      <TableContainer component={Paper}>
        <Table sx={{ width: "100%" }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Type</TableCell>
              <TableCell align="left">Date</TableCell>
              <TableCell align="left">Montant</TableCell>
              <TableCell align="left">Statut</TableCell>
              {/* <TableCell align="left">Réçu</TableCell> */}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow
                key={row.name}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
              >
                {user.idVerified === "1" && (
                  <TableCell component="th" scope="row">
                    {row.name}
                  </TableCell>
                )}

                {user.idVerified === "1" && (
                  <TableCell align="left">{user.dateVerified}</TableCell>
                )}
                {user.idVerified === "1" && (
                  <TableCell align="left">2500</TableCell>
                )}
                {user.idVerified === "1" && (
                  <TableCell align="left">
                    <Typography sx={{ color: "green" }}>Payé</Typography>
                  </TableCell>
                )}
                {/* {user.idVerified === "1" && (
                  <TableCell align="left">
                    <Button>
                      <a
                        href={`../../borderau/reussie/${user._id}_carte.pdf`}
                        target="blank"
                        download
                      >
                        {row.recu}
                      </a>
                    </Button>
                  </TableCell>
                )} */}

                {user.idVerified === "0" && (
                  <TableCell component="th" scope="row">
                    {row.name}
                  </TableCell>
                )}

                {user.idVerified === "0" && (
                  <TableCell align="left">{user.dateVerified}</TableCell>
                )}
                {user.idVerified === "0" && (
                  <TableCell align="left">0</TableCell>
                )}
                {user.idVerified === "0" && (
                  <TableCell align="left">
                    <Typography color="error">Echoué</Typography>
                  </TableCell>
                )}
                {user.idVerified === "0" && (
                  <TableCell align="left">
                    <Button>
                      <a
                        href={`../../borderau/echec/${user._id}_carte.pdf`}
                        target="blank"
                        download
                      >
                        {row.recu}
                      </a>
                    </Button>
                  </TableCell>
                )}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <CalendarComponent />
    </Box>
  );
}
