import {
  Alert,
  Button,
  CircularProgress,
  Grid,
  Snackbar,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { addPost } from "../../../features/calendar.reducers";

const Event = () => {
  const [title, setTitle] = useState("");
  const [start, setstart] = useState("");
  const [end, setend] = useState("");
  const [location, setlocation] = useState("");
  const [description, setdescription] = useState("");

  const [open, setOpen] = React.useState(false);
  const [error, setError] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const dispatch = useDispatch();

  const handlePost = async (e) => {
    e.preventDefault();
    setLoading(true);
    const data = new FormData(e.currentTarget);
    axios
      .post(`${process.env.REACT_APP_URL_CALENDAR}/create`, {
        title: data.get("title"),
        start: data.get("start"),
        //"2023-04-12T07:00:00.000Z",
        end: data.get("end"),
        location: data.get("location"),
        description: data.get("description"),
      })
      .then((res) => {
        // console.log(res)

        dispatch(addPost(res));
        setLoading(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(res.data.message);
      })
      .catch((err) => {
        setLoading(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };
  return (
    <div>
      {resToHome ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Typography component="h1" variant="h5">
          Créer un évènement
        </Typography>
        <form onSubmit={handlePost}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                fullWidth
                required
                id="title"
                label="Title"
                name="title"
                onChange={(e) => setTitle(e.target.value)}
                value={title}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="start"
                label="start"
                name="start"
                helperText="2023-04-12T07:00:00.000Z"
                value={start}
                onChange={(e) => {
                  setstart(e.target.value);
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="end"
                label="end"
                name="end"
                helperText="2023-04-12T20:00:00.000Z"
                value={end}
                onChange={(e) => {
                  setend(e.target.value);
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="location"
                label="location"
                name="location"
                value={location}
                onChange={(e) => {
                  setlocation(e.target.value);
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                multiline
                rows={4}
                id="description"
                label="description"
                name="description"
                value={description}
                onChange={(e) => {
                  setdescription(e.target.value);
                }}
              />
            </Grid>
          </Grid>
          {loading ? (
            <CircularProgress />
          ) : (
            <Button type="submit" variant="contained" sx={{ mt: 3, mb: 2 }}>
              Envoyer
            </Button>
          )}
        </form>
      </Box>
    </div>
  );
};

export default Event;
