import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import {
  Button,
  FormControl,
  Icon,
  Input,
  InputLabel,
  Typography,
} from "@mui/material";
import { NavLink } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getAllpartition } from "../../../features/partition.reducers";
import { useEffect, useState } from "react";
import CardPartition from "../card/CardPartition";
import { Stack } from "@mui/system";

import { Card, CardContent, CardMedia } from "@mui/material";

import { styled } from "@mui/material/styles";

import axios from "axios";

const AudioPlayerWrapper = styled(Card)({
  maxWidth: 600,
  margin: "auto",
});

const AudioPlayerImage = styled(CardMedia)({
  height: 130,
});

const AudioPlayerControls = styled("div")({
  display: "flex",
  alignItems: "center",
});

const Partition = () => {
  /**State global */

  const dispatch = useDispatch();
  const partitions = useSelector((state) => state.partitions.partitions);
  const [searchValue, setSearchValue] = useState("");
  const user = useSelector((state) => state.user.user);

  const [categoriesFiltre] = useState(["Soprano", "All"]);
  const [categoriesFiltreAlto] = useState(["Alto", "All"]);
  const [categoriesFiltreTenor] = useState(["Ténor", "All"]);
  const [categoriesFiltreBasse] = useState(["Basse", "All"]);

  const filtreObjets = () => {
    return partitions.filter((objet) => {
      const categorieIncluse = categoriesFiltre.includes(objet.categorie);

      const recherIncluse = objet.title
        .toLowerCase()
        .includes(searchValue.toLocaleLowerCase());
      return categorieIncluse && recherIncluse;
    });
  };
  const filtreObjetsAlto = () => {
    return partitions.filter((objet) => {
      const categorieIncluse = categoriesFiltreAlto.includes(objet.categorie);

      const recherIncluse = objet.title
        .toLowerCase()
        .includes(searchValue.toLocaleLowerCase());
      return categorieIncluse && recherIncluse;
    });
  };
  const filtreObjetTenor = () => {
    return partitions.filter((objet) => {
      const categorieIncluse = categoriesFiltreTenor.includes(objet.categorie);

      const recherIncluse = objet.title
        .toLowerCase()
        .includes(searchValue.toLocaleLowerCase());
      return categorieIncluse && recherIncluse;
    });
  };
  const filtreObjetBasse = () => {
    return partitions.filter((objet) => {
      const categorieIncluse = categoriesFiltreBasse.includes(objet.categorie);

      const recherIncluse = objet.title
        .toLowerCase()
        .includes(searchValue.toLocaleLowerCase());
      return categorieIncluse && recherIncluse;
    });
  };

  const handleChangeTerme = (event) => {
    setSearchValue(event.target.value);
  };

  const partitionsMap = filtreObjets();
  const partitionsMapAto = filtreObjetsAlto();
  const partitionsMapTenor = filtreObjetTenor();
  const partitionsMapBasse = filtreObjetBasse();
  // const [search, setSearch] = useState("");

  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };

  /**Au chargement de la page */

  useEffect(() => {
    sendRquestPost().then((data) => {
      dispatch(getAllpartition(data));
    });
  }, [dispatch]);

  return (
    <div
      style={{
        backgroundImage: `url('./assets/portee.jpg')`,
        backgroundPosition: "center",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        width: "100%",
        height: "100vh",
        backgroundColor:
          "rgba(128, 128, 128, 0.5)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
      }}
    >
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      <br />
      <br />

      <>
        {user.partition === "Percussion" && (
          <>
            <h2 style={{ textAlign: "center" }}>
              Les audio et vidéos spécifiques aux percussionnistes
            </h2>
            {partitions
              ?.filter((el) => {
                if (el.categorie === "Percussion") return true;
              })
              .map((partition) => {
                return (
                  <>
                    {(partition.partition.split(".").pop() === "ogg" ||
                      partition.partition.split(".").pop() === "mp3" ||
                      partition.partition.split(".").pop() === "wav" ||
                      partition.partition.split(".").pop() === "aac" ||
                      partition.partition.split(".").pop() === "flac") && (
                      <AudioPlayerWrapper
                        sx={{ display: "flex", justifyContent: "space-around" }}
                        key={partition._id}
                        variant="outlined"
                      >
                        <AudioPlayerImage component="img" image="./logo.jpg" />

                        <CardContent>
                          <Typography variant="h6" component="h2">
                            {partition.auteur}
                          </Typography>
                          <Typography color="textSecondary">
                            Percussion Tuto
                          </Typography>
                          <AudioPlayerControls></AudioPlayerControls>
                          <audio controls>
                            <source
                              src={partition.partition}
                              type="audio/ogg"
                            ></source>
                            <source
                              src={partition.partition}
                              type="audio/mpeg"
                            ></source>
                            Votre navigateur ne supporte pas la lecture de
                            fichiers audio.
                          </audio>
                        </CardContent>
                        {user.isAdminDev && (
                          <Button
                            color="error"
                            variant="contained"
                            onClick={() => {
                              if (
                                window.confirm(
                                  "Voulez vous vraiment supprimez ?"
                                )
                              ) {
                                axios
                                  .delete(
                                    `${process.env.REACT_APP_URL_USER}/delete_partition/${partition._id}`
                                  )
                                  .then((res) => {
                                    alert(res.data.message);
                                  })
                                  .catch((err) =>
                                    alert(err.response.data.message)
                                  );
                              }
                            }}
                          >
                            Supprimer{" "}
                          </Button>
                        )}
                      </AudioPlayerWrapper>
                    )}

                    {
                      // ( (partition.partition).split('.').pop() === "mp4" || (partition.partition).split('.').pop() === "avi" || (partition.partition).split('.').pop() === "mkv" || (partition.partition).split('.').pop() === "mov" || (partition.partition).split('.').pop() === "wmv") && <VideoPlayerWrapper key={partition._id}>
                      //       <CardActionArea onClick={togglePlaying}>
                      //         <video
                      //           poster='./logo.jpg'
                      //          controls
                      //           style={{ width: "100%" }}
                      //         >
                      //           <source src={partition.partition} type="video/mp4" />
                      //           Your browser does not support the video tag.
                      //         </video>
                      //       </CardActionArea>
                      //       <CardContent>
                      //         <Typography variant="h5" component="h2">
                      //           {partition.title}
                      //         </Typography>
                      //         <Typography color="textSecondary">
                      //           {partition.views} views •
                      //         </Typography>
                      //         <Typography variant="body2" color="textSecondary">
                      //           {partition.description}
                      //         </Typography>
                      //       </CardContent>
                      //        { user.isAdminDev &&
                      //                  <Button  color='error' variant='contained' onClick={() => {
                      //                         if (window.confirm('Voulez vous vraiment supprimez ?')) {
                      //                          axios.delete(`${process.env.REACT_APP_URL_USER}/delete_partition/${partition._id}`).then((res) => {
                      //     alert(res.data.message)
                      //   }).catch(err=>alert(err.response.data.message))
                      //                         }
                      //                       }} >Supprimer </Button>
                      //     }
                      //                 </VideoPlayerWrapper>
                    }
                  </>
                );
              })}
          </>
        )}
      </>
      <>
        {user.partition === "Percussion" && (
          <a
            href="https://chat.whatsapp.com/GC3wAizLThmGrVAdQlqEMX"
            rel="noopener noreferrer"
            target="_blank"
          >
            Cliquez ici pour rejoindre votre groupe whatsapp {user.partition}
          </a>
        )}
      </>
      <>
        {user.partition === "Soprano" && (
          <a
            href="https://chat.whatsapp.com/JCuNaCFZJW26jZ4Fe20xeH"
            rel="noopener noreferrer"
            target="_blank"
          >
            Cliquez ici pour rejoindre votre groupe whatsapp {user.partition}
          </a>
        )}
      </>
      <>
        {user.partition === "Ténor" && (
          <a
            href="https://chat.whatsapp.com/KuiR3hlS2g0lu6xr31R7yX"
            rel="noopener noreferrer"
            target="_blank"
          >
            Cliquez ici pour rejoindre votre groupe whatsapp {user.partition}
          </a>
        )}
      </>
      <>
        {user.partition === "Basse" && (
          <a
            href="https://chat.whatsapp.com/EIHYxQav3goB5faHpKWEMR"
            rel="noopener noreferrer"
            target="_blank"
          >
            Cliquez ici pour rejoindre votre groupe whatsapp {user.partition}
          </a>
        )}
      </>
      <>
        {user.partition === "Alto" && (
          <a
            href="https://chat.whatsapp.com/GQpO9xyLlG23HM2jWskhFT"
            rel="noopener noreferrer"
            target="_blank"
          >
            Cliquez ici pour rejoindre votre groupe whtasapp {user.partition}
          </a>
        )}
      </>

      <br></br>

      <h2 style={{ textAlign: "center" }}>
        Toutes vos partitions {user.partition}s.
      </h2>
      <br />
      {/* <Typography variant="h6" sx={{ textAlign: "center" }}>
        Toutes vos partitions {user.partition}s des morceaux.
      </Typography> */}
      <FormControl fullWidth sx={{ m: 1 }} variant="standard">
        <InputLabel htmlFor="standard-adornment-amount">
          {" "}
          Réchercher un morceau
        </InputLabel>
        <Input
          fullWidth
          value={searchValue}
          // onChange={(e) => setSearchValue(e.target.value)}
          onChange={handleChangeTerme}
          id="standard-adornment-amount"
          // startAdornment={
          //   <InputAdornment position="start">Partition</InputAdornment>
          // }
        />
      </FormControl>
      <br />
      <Stack
        direction="row"
        spacing={6}
        alignItems="center"
        justifyContent="center"
      >
        <h3>Titre</h3>
        <h3>Auteur</h3>
        <h3>Gamme</h3>
      </Stack>
      <br></br>

      <div>
        {(user?.partition === "Soprano" || user?.partition === "Percussion") &&
          partitionsMap?.map((partition) => {
            return <CardPartition partition={partition} />;
          })}
      </div>
      <div>
        {user?.partition === "Alto" &&
          partitionsMapAto?.map((partition) => {
            return <CardPartition partition={partition} />;
          })}
      </div>
      <div>
        {user?.partition === "Ténor" &&
          partitionsMapTenor?.map((partition) => {
            return <CardPartition partition={partition} />;
          })}
      </div>
      <div>
        {user?.partition === "Basse" &&
          partitionsMapBasse?.map((partition) => {
            return <CardPartition partition={partition} />;
          })}
      </div>
      {/* {(user?.partition === "Basse" ||
        user?.partition === "Soprano" ||
        user?.partition === "Alto" ||
        user?.partition === "Ténor" ||
        user?.partition === "Percussion") &&
        partitions
          ?.filter((el) => {
            if (el.categorie === "All") return true;
          })
          .map((partition) => {
            return <CardPartition partition={partition} />;
          })} */}
    </div>
  );
};

export default Partition;
