import * as React from "react";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { NavLink } from "react-router-dom";
import { Icon } from "@mui/material";
import { useSelector } from "react-redux";
import Lispost from "./Lispost";

export default function TextMobileStepper() {
  const user = useSelector((state) => state.user?.user);

  // Exemple d'utilisation
  // Affichera la date actuelle au format JJ/MM/AAAA (par exemple, 14/03/2023 pour le 14 mars 2023)

  return (
    <Box
      sx={{
        backgroundImage: `url('./Tori/soprano18.jpg')`,
        backgroundPosition: "center",
        height: "100vh",
        width: "100%",
        backgroundSize: "cover",
        backgroundColor:
          "rgba(128, 128, 128, 1)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
      }}
    >
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      <h2 style={{ textAlign: "center" }}>
        Note d'évaluation obtenue lors des compositions
      </h2>

      {user.souscription === "" ? (
        <>
          <Typography variant="subtitle2">
            Compte non souscrire, veuilllez vous souscrire pour profiter des
            avantages qu'offre le groupe fanfare la Grâce Parle. Shalom!
          </Typography>
          <Button variant="contained" size="small">
            <a
              style={{ textDecoration: "none" }}
              href="https://me.fedapay.com/souscription_1234"
              target="blank"
            >
              Souscrire
            </a>
          </Button>
          {/* <Typography variant='subtitle2'>Après la souscription,veuillez vous inscire sur FINANFA en cliquant sur ce lien : 
            <Button variant="filed" size="small">
        <a
          style={{ textDecoration: "none" }}
          href="https://network.finanfa.africa/login/signup/7z3KxBg5DArlb9eQ8JLn"
          target="blank"
        >
        Inscription sur FINANFA
        </a>
          </Button> </Typography> */}
        </>
      ) : (
        user?.average !== "" && (
          <Box>
            {/* <Typography variant='h6' sx={{textAlign:'center'}} > Evaluation </Typography> */}
            <h3>Note obtenue : {user.average}/20 </h3>

            {user.sexe === "Masculin" && user.average < "10" && (
              <>
                <Typography variant="subtitle2">
                  Si vous avez obtenu une moyenne en dessous de 10, il est
                  important de prendre conscience que cela peut être un signe
                  d'alarme concernant votre performance musicale au sein du
                  groupe fanfare. Les notes ne reflètent pas toujours la valeur
                  d'une personne, mais dans le contexte musical, elles sont un
                  indicateur important de la compréhension des partitions et de
                  la performance globale d'un musicien.
                </Typography>
                <Typography variant="subtitle2">
                  Obtenir une note en dessous de 10 peut être un signal que vous
                  ne comprenez pas bien la partition ou que vous ne travaillez
                  pas assez dur pour jouer avec précision et musicalité. Si vous
                  ne réagissez pas rapidement pour améliorer votre performance,
                  vous risquez de vous retrouver dans une situation difficile au
                  sein du groupe. Il est important de se rappeler que le monde
                  de la musique est très compétitif et que les résultats que
                  vous obtenez peuvent avoir un impact significatif sur votre
                  avenir musical. Les musiciens qui ont des compétences
                  musicales limitées ont tendance à avoir des opportunités
                  professionnelles plus limitées et peuvent avoir des
                  difficultés à progresser dans leur carrière.
                </Typography>
                <Typography variant="subtitle2">
                  Il est donc essentiel de prendre des mesures pour améliorer
                  votre performance musicale dès que possible. Cela peut inclure
                  l'adoption d'une approche plus stratégique pour étudier les
                  partitions, la recherche d'aide supplémentaire auprès d'un
                  enseignant ou d'un membre expérimenté du groupe, et
                  l'engagement plus actif lors des répétitions.
                </Typography>
              </>
            )}
            {user.sexe === "Masculin" &&
              user.partition === "Percussion" &&
              user.average < "10" && (
                <Typography variant="subtitle2">
                  En tant que percussionniste dans notre groupe fanfare, nous
                  avons remarqué que votre jeu de tambours est solide, mais nous
                  sommes préoccupés par votre manque d'intérêt pour la théorie
                  musicale. Vous ne pouvez pas ignorer l'importance de la
                  théorie musicale, car elle est essentielle pour comprendre les
                  nuances de la musique et pour améliorer votre jeu. Nous vous
                  encourageons à prendre le temps d'étudier la théorie musicale
                  et à appliquer ces connaissances dans votre pratique. Cela
                  vous aidera à exceller dans votre jeu de percussion et à
                  contribuer de manière plus significative à notre groupe.
                  N'oubliez pas que la pratique et la théorie musicale vont de
                  pair pour créer une expérience musicale riche et complète.
                </Typography>
              )}
            {(user.sexe === "Masculin" || user.sexe === "Féminin") &&
              user.partition === "Percussion" &&
              user.average > "10" && (
                <Typography variant="subtitle2">
                  En tant que percussionniste, vous apportez une contribution
                  essentielle à notre groupe fanfare. Mais en plus de votre
                  excellent travail sur les tambours, nous sommes également
                  impressionnés par votre connaissance de la théorie musicale.
                  Votre capacité à comprendre les nuances de la musique et à
                  appliquer ces connaissances dans votre jeu est un atout
                  inestimable pour notre groupe. Continuez à approfondir vos
                  connaissances de la théorie musicale et à trouver des moyens
                  de les appliquer dans votre jeu de percussion. Nous sommes
                  fiers de vous avoir comme membre de notre groupe et nous
                  sommes impatients de voir où votre talent et votre travail
                  acharné vous mèneront.
                </Typography>
              )}

            {user.sexe !== "Masculin" && user.average < "10" && (
              <Typography variant="subtitle2">
                En tant que musicienne dans un groupe fanfare, vous savez que
                chaque note compte. Obtenez une note en dessous de 10 peut être
                préoccupant, mais cela peut être un signe que vous devez
                travailler plus dur et vous concentrer sur votre pratique.
                Utilisez cette expérience pour vous motiver à vous améliorer et
                à exceller dans votre musique.
              </Typography>
            )}

            {user.average >= "10" && user.average <= "14" && (
              <Typography variant="subtitle2">
                Vous avez obtenu une note entre 10 et 15, félicitations pour vos
                efforts et votre réussite à atteindre un niveau de compétence de
                base dans votre performance musicale au sein du groupe fanfare.
                C'est un bon début, mais cela ne signifie pas que vous ne pouvez
                pas continuer à vous améliorer. Il est important de se rappeler
                que la musique est une discipline qui nécessite de la pratique
                et de l'engagement pour atteindre un niveau élevé de compétence.
                Les notes que vous obtenez peuvent être un indicateur de votre
                performance, mais elles ne reflètent pas toujours l'ensemble de
                vos efforts et de votre potentiel. Si vous voulez améliorer
                votre performance musicale, il est important de travailler sur
                vos compétences de base telles que la compréhension de la
                partition et la technique de jeu. Cela peut être fait en
                travaillant avec un enseignant ou un membre expérimenté du
                groupe qui peut vous donner des conseils et des astuces pour
                améliorer votre performance.
              </Typography>
            )}
            {user.average > "14" && (
              <Typography variant="subtitle2">
                Félicitations pour avoir obtenu une note au-dessus de 15 ! C'est
                une preuve de votre travail acharné et de votre talent en tant
                que musicien dans le groupe fanfare. Continuez à vous concentrer
                sur votre pratique et votre apprentissage pour maintenir et
                améliorer votre niveau de performance. Vous êtes un exemple pour
                vos camarades de groupe, alors continuez à inspirer et à
                encourager les autres membres à atteindre leur plein potentiel.
                N'oubliez pas de prendre le temps de célébrer vos succès, tout
                en restant humble et en cherchant toujours à vous améliorer.
                Continuez à travailler avec détermination et passion, et vous
                continuerez à exceller dans votre musique.
              </Typography>
            )}
          </Box>
        )
      )}
      {user.average === "" && (
        <Typography variant="subtitle2">
          Aucune note disponible pour le moment{" "}
        </Typography>
      )}

      <Typography textAlign="center" variant="h6">
        Communiqué urgent{" "}
      </Typography>

      <Lispost />
    </Box>
  );
}
