import React, { useEffect } from "react";
import { getAllImages } from "../../../features/images.reducers";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { Button } from "@mui/material";
import Navbar from "../NavbarAdmin";
const DataImage = () => {
  /**State global */
  const dispatch = useDispatch();
  const images = useSelector((state) => state.images.images);
  /**Récuperer touts nos iimages */
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_PICTURE}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };

  /**Au chargement de la page */

  useEffect(() => {
    sendRquestPost().then((data) => {
      dispatch(getAllImages(data));
    });
  }, [dispatch]);
  return (
    <div>
      <Navbar />
      {images?.map((image) => {
        return (
          <>
            <img
              style={{ width: "100%", height: "auto" }}
              src={image.picture}
              alt="image_picture"
            />
            <p>{image.titre}</p>
            <p>{image.category}</p>
            <p>{image.partition}</p>
            <Button
              onClick={() => {
                axios
                  .delete(`${process.env.REACT_APP_URL_PICTURE}/${image._id}`)
                  .then((res) => {
                    alert(res.data.message);
                  })
                  .catch((err) => alert(err.response.data.message));
              }}
            >
              Supprimez image
            </Button>
          </>
        );
      })}
    </div>
  );
};

export default DataImage;
