import axios from "axios";

import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setgetAll } from "../../../features/users.reducers";
import { getAllExercices } from "../../../features/exercices.reducers";
import Post from "./Card";

const Lispost = () => {
  const dispatch = useDispatch();
  const allPosts = useSelector((state) => state.exercices.exercices);

  const [loadPost, setloadPost] = useState(true);
  const [count, setcount] = useState(10);

  // Récuperer touts les utilisateurs
  const sendRquestUsers = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}`, {
      withCredentials: true,
    });
    // .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };
  // Recuperer touts les posts
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_EXERCICES}`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };
  const loadMore = () => {
    if (
      window.innerHeight + document.documentElement.scrollTop + 1 >
      document.scrollingElement.scrollHeight
    ) {
      setloadPost(true);
    }
  };

  //   useEffect(() => {
  // getUserWithVerifyToken().then((data) => {
  //   dispatch(setGetUser(data.user));
  // });
  //   }, []);

  useEffect(() => {
    sendRquestUsers().then((data) => {
      dispatch(setgetAll(data));
      //  console.log(data);
    });
  }, [dispatch]);

  useEffect(() => {
    if (loadPost) {
      sendRquestPost().then((data) => {
        dispatch(getAllExercices(data));
      });

      setcount(count + 5);
      setloadPost(false);
    }
    window.addEventListener("scroll", loadMore);
    return () => window.removeEventListener("scroll", loadMore);
  }, [count, dispatch, loadPost]);

  return (
    <div>
      {allPosts?.map((exercice) => {
        return (
          <ul>
            <Post exercice={exercice} />
          </ul>
        );
      })}
    </div>
  );
};

export default Lispost;
