import * as React from "react";
import Box from "@mui/material/Box";
import Collapse from "@mui/material/Collapse";
import IconButton from "@mui/material/IconButton";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import DownloadIcon from "@mui/icons-material/Download";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import axios from "axios";
import { Button } from "@mui/material";

function createData(name, calories, fat, price) {
  return {
    name,
    calories,
    fat,

    price,
    history: [
      {
        date: "2020-01-05",
        customerId: "11091700",
        amount: 3,
      },
      {
        date: "2020-01-02",
        customerId: "Anonymous",
        amount: 1,
      },
    ],
  };
}

export default function CollapsibleTable({ partition }) {
  const id = partition?.partition;

  const s = id.slice(47);

  const user = useSelector((state) => state.user.user);
  function Row(props) {
    const { row } = props;
    const [open, setOpen] = React.useState(false);

    const handleDelete = async () => {
      await axios
        .delete(
          `${process.env.REACT_APP_URL_USER}/delete_partition/${partition._id}`
        )
        .then((res) => {
          alert(res.data.message);
        })
        .catch((err) => alert(err.response.data.message));
    };

    return (
      <div>
        <TableRow
          key={partition._id}
          sx={{ "& > *": { borderBottom: "unset" } }}
        >
          <TableCell>
            <IconButton
              aria-label="expand row"
              size="small"
              onClick={() => setOpen(!open)}
            >
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </TableCell>
          <TableCell component="th" scope="row">
            <Typography variant="subtitle2">{row.name}</Typography>
          </TableCell>
          <TableCell align="left">
            <Typography variant="subtitle2">{row.calories}</Typography>
          </TableCell>
          <TableCell align="right">
            {" "}
            <Typography variant="subtitle2">{row.fat}</Typography>
          </TableCell>
        </TableRow>
        <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Box sx={{ margin: 1 }}>
                <Typography
                  variant="h6"
                  gutterBottom
                  component="div"
                ></Typography>

                {id.split(".").pop() === "jpg" ||
                id.split(".").pop() === "png" ||
                id.split(".").pop() === "jpeg" ? (
                  <NavLink to={`/partitions/${s}`}>
                    <img
                      src={partition.partition}
                      alt="image_partition"
                      width="100%"
                    />
                  </NavLink>
                ) : (
                  <>
                    <a href={id} target="_blank" rel="noopener noreferrer">
                      <img
                        style={{
                          objectFit: "cover",
                          width: "100%",
                          height: "65px",
                        }}
                        src="./pdf.png"
                        alt="partitions pdf"
                      />
                    </a>
                    <IconButton
                      component="a"
                      href={id}
                      download
                      sx={{ cursor: "pointer" }}
                    >
                      <DownloadIcon />
                    </IconButton>
                    <IconButton
                      target="_blank"
                      rel="noopener noreferrer"
                      component="a"
                      href={id}
                    >
                      <RemoveRedEyeIcon />
                    </IconButton>
                    {/* <a style={{textDecoration:'none',paddingLeft:"20px"}} href={id} target="_blank" rel="noreferrer" ><RemoveRedEyeIcon /></a> */}
                  </>
                )}

                {user.isAdminDev && (
                  <Button
                    color="error"
                    variant="contained"
                    onClick={() => {
                      if (
                        window.confirm(
                          "Voulez vous vraiment supprimez cette partition ?"
                        )
                      ) {
                        // eslint-disable-next-line no-lone-blocks
                        {
                          handleDelete();
                        }
                      }
                    }}
                  >
                    Supprimer{" "}
                  </Button>
                )}
              </Box>
            </Collapse>
          </TableCell>
        </TableRow>
      </div>
    );
  }

  const rows = [
    createData(partition?.title, partition?.auteur, partition?.gamme),
  ];

  return (
    <TableContainer
      sx={{
        // backgroundColor: "#C1233A",
        color: "white",
      }}
      component={Paper}
      elevation={3}
    >
      <Table aria-label="collapsible table">
        <TableBody>
          {rows?.map((row) => (
            <Row key={row.name} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
