import {
  Alert,
  Button,
  CircularProgress,
  FormControl,
  Input,
  InputAdornment,
  InputLabel,
  Snackbar,
  Stack,
  Table,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setGetUser } from "../../../features/user.reducers";
import { setgetAll } from "../../../features/users.reducers";
import Navbar from "../NavbarAdmin";
import BasicTable from "./Table";
import { getUserWithVerifyToken, sendRquest } from "../../../app/verify";

const Parent = () => {
  const users = useSelector((state) => state.users.users);
  const user = useSelector((state) => state.user.user);

  const dispatch = useDispatch();
  const [searchValue, setSearchValue] = useState("");
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [loadingTwo, setLoadingTwo] = React.useState(false);
  const [error, setError] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const SendPdfListe = async (e) => {
    setLoading(true);
    await axios
      .get(`${process.env.REACT_APP_URL_USER}/note`)
      .then((res) => {
        setLoading(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(res.data.message);
      })
      .catch((err) => {
        // console.log(err);
        setLoading(false);
        setOpen(true);
        if (err.response.data.message) setError(err.response.data.message);
      });
  };
  const onResetListeAll = async (e) => {
    setLoadingTwo(true);
    await axios
      .put(`${process.env.REACT_APP_URL_USER}/resetAllEvaluer`)
      .then((res) => {
        setLoadingTwo(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(res.data.message);
      })
      .catch((err) => {
        setLoadingTwo(false);
        setOpen(true);
        if (err.response.data.message) setError(err.response.data.message);
      });
  };

  useEffect(() => {
    getUserWithVerifyToken().then((data) => {
      dispatch(setGetUser(data.user));
    });
  }, [dispatch]);
  useEffect(() => {
    sendRquest().then((data) => {
      dispatch(setgetAll(data));
    });
  }, [dispatch]);
  return (
    <>
      {user?.isAdminPupitre && (
        <div>
          {resToHome ? (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="success"
                  sx={{ width: "100%" }}
                >
                  {succees}
                </Alert>
              </Snackbar>
            </Stack>
          ) : (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="error"
                  sx={{ width: "100%" }}
                >
                  {error}
                </Alert>
              </Snackbar>
            </Stack>
          )}
          <Navbar />
          <Typography>Rechercher avec son code </Typography>
          <FormControl fullWidth sx={{ m: 1 }} variant="standard">
            <InputLabel htmlFor="standard-adornment-amount">
              {" "}
              Code Personnel
            </InputLabel>
            <Input
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
              id="standard-adornment-amount"
              startAdornment={
                <InputAdornment position="start">Code</InputAdornment>
              }
            />
          </FormControl>

          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Nom</TableCell>
                <TableCell align="left">Prénom</TableCell>
                <TableCell align="left">Code</TableCell>
                <TableCell align="center">Note</TableCell>
              </TableRow>
            </TableHead>
          </Table>

          {users
            ?.filter(
              (item) =>
                item.identification
                  ?.toLowerCase()
                  .includes(searchValue.toLowerCase()) &&
                item.isSuperAdmin !== true
            )
            .map((el) => {
              return <BasicTable key={el?._id} el={el} />;
            })}

          <br></br>

          {loading ? (
            <CircularProgress />
          ) : (
            <Button
              type="submit"
              onClick={SendPdfListe}
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Envoyer le fichier
            </Button>
          )}

          {loadingTwo ? (
            <CircularProgress />
          ) : (
            <Button
              type="submit"
              onClick={onResetListeAll}
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Remettre à zéro
            </Button>
          )}
        </div>
      )}
    </>
  );
};

export default Parent;
