import axios from "axios";
import { useDispatch } from "react-redux";
import * as React from "react";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Navbar from "../NavbarAdmin";
import { addpartition } from "../../../features/partition.reducers";
import Event from "./Event";
import Audio from "./Audios";
import Video from "./Video";
import Image from "./Image";
import { Alert, CircularProgress, MenuItem, Snackbar } from "@mui/material";
import AudiotrackIcon from "@mui/icons-material/Audiotrack";
import { InputAdornment } from "@mui/material";
import { Stack } from "react-bootstrap";

const theme = createTheme();
const currencies = [
  {
    value: "Soprano",
    label: "Soprano",
  },
  {
    value: "Alto",
    label: "Alto",
  },
  {
    value: "Ténor",
    label: "Ténor",
  },
  {
    value: "Basse",
    label: "Basse",
  },
  {
    value: "Percussion",
    label: "Percussion",
  },
  {
    value: "All",
    label: "All",
  },
];

export default function Morceau() {
  /**State local */
  const [titles, setTitles] = React.useState("");
  const [auteur, setauteur] = React.useState("");
  const [gamme, setgamme] = React.useState("");
  const [categorie, setcategorie] = React.useState("");
  const [file, setFile] = React.useState();
  const [error, setError] = React.useState("");
  const dispatch = useDispatch();

  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  /**Function pour créer nos partitions */
  const handlePostUpload = async () => {
    const data = new FormData();
    data.append("title", titles);
    data.append("gamme", gamme);
    data.append("auteur", auteur);
    data.append("categorie", categorie);
    data.append("partition", file);

    const res = await axios.post(
      `${process.env.REACT_APP_URL_USER}/create`,
      data
    );

    const datas = await res.data;
    return datas;
  };
  const handlePost = async (e) => {
    e.preventDefault();
    setLoading(true);
    handlePostUpload()
      .then((datas) => {
        dispatch(addpartition(datas));
        setLoading(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(datas.message);
        // alert(datas.message)
      })
      .catch((err) => {
        setLoading(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };

  return (
    <>
      {resToHome ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
      {
        <ThemeProvider theme={theme}>
          <Navbar />
          <Container component="main">
            <CssBaseline />
            <Box
              sx={{
                marginTop: 8,
                display: "flex",
                flexDirection: "column",

                width: "100%",
              }}
            >
              <Typography align="center" component="h1" variant="h5">
                Créer le répertoire de chaque partition
              </Typography>
              <form onSubmit={handlePost}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      required
                      id="title"
                      label="Title"
                      name="title"
                      onChange={(e) => setTitles(e.target.value)}
                      value={titles}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="auteur"
                      label="auteur"
                      name="auteur"
                      value={auteur}
                      onChange={(e) => {
                        setauteur(e.target.value);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="gamme"
                      label="gamme"
                      name="gamme"
                      value={gamme}
                      onChange={(e) => {
                        setgamme(e.target.value);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      name="file"
                      onChange={(e) => {
                        setFile(e.target.files[0]);
                      }}
                      type="file"
                      id="file"
                      accept=".jpg, .jpeg, .png"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      margin="normal"
                      required
                      id="categorie"
                      select
                      label="Partition"
                      defaultValue=""
                      name="categorie"
                      value={categorie}
                      onChange={(e) => {
                        setcategorie(e.target.value);
                      }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <AudiotrackIcon />
                          </InputAdornment>
                        ),
                      }}
                    >
                      {currencies.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </TextField>
                  </Grid>
                </Grid>
                {loading ? (
                  <CircularProgress />
                ) : (
                  <Button
                    type="submit"
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                  >
                    Envoyer
                  </Button>
                )}
              </form>
            </Box>
          </Container>
        </ThemeProvider>
      }
      {/* <EVentMessage/> */}

      <Event />
      <Audio />
      <Video />
      <Image />
    </>
  );
}
