import { Icon } from "@mui/material";
import React from "react";
import { NavLink, useParams } from "react-router-dom";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

const PartitionImage = () => {
  // http://localhost:7200/image/partition/tout-joyeux.jpeg
  const params = useParams();
  // eslint-disable-next-line no-unused-vars
  const id = `./${window.location.href}`;
  // console.log(`https://lgpfanfare-dalo.onrender.com/partition/${params.id}`);
  const idTo = `https://lgpfanfare-dalo.onrender.com/partition/${params.id}`;

  return (
    <div
      style={{
        backgroundColor: "#000",
      }}
    >
      <NavLink to="/partition">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      {/* http://localhost:3000/image/partition/tout-joyeux.jpeg */}

      <img
        style={{
          objectFit: "contain",
          height: "100vh",
          width: "100%",
        }}
        src={`${idTo}`}
        alt="partition personnele"
      />
    </div>
  );
};

export default PartitionImage;
