import React, { useState } from "react";
import {
  Card,
  CardActionArea,
  CardContent,
  Typography,
  Button,
} from "@mui/material";

import { styled } from "@mui/material/styles";
import axios from "axios";
import { useSelector } from "react-redux";

const VideoPlayerWrapper = styled(Card)({
  maxWidth: 600,
  margin: "auto",
});

const Video = ({ video }) => {
  const [playing, setPlaying] = useState(false);
  const user = useSelector((state) => state.user.user);
  const userId = user?._id;
  const handleviews = async () => {
    await axios.post(
      `${process.env.REACT_APP_URL_VIDEO}/views/${video._id}/${userId}`
    );
    // .then((res) => {
    //   // alert(res.data.massage)
    // })
    // .catch((err) => {
    //   console.log(err);
    // });
  };
  const togglePlaying = () => {
    setPlaying(!playing);
  };

  const handleDelete = async () => {
    axios
      .delete(`${process.env.REACT_APP_URL_VIDEO}/delete_video/${video._id}`)
      .then((res) => {
        // console.log(res);
      })
      .catch((err) => alert(err.response.data.message));
  };

  return (
    <div>
      <VideoPlayerWrapper key={video._id}>
        <CardActionArea
          // onClick={togglePlaying}
          onClick={handleviews}
        >
          {/* <VideoPlayerPoster component="img" image={video.poster} />
        
        */}
          <video poster="./logo.jpg" controls style={{ width: "100%" }}>
            <source src={video?.videoUrl} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </CardActionArea>
        <CardContent>
          <Typography variant="h5" component="h2">
            {video.title}
          </Typography>
          <Typography color="textSecondary">{video.views} vues •</Typography>
          <Typography variant="body2" color="textSecondary">
            {video.description}
          </Typography>
        </CardContent>
        {user.isAdminDev && (
          <Button
            color="error"
            variant="contained"
            onClick={() => {
              if (window.confirm("Voulez vous vraiment supprimez ?")) {
                // eslint-disable-next-line no-lone-blocks
                {
                  handleDelete();
                }
              }
            }}
          >
            Supprimer{" "}
          </Button>
        )}
      </VideoPlayerWrapper>
    </div>
  );
};

export default Video;
