import React, { useEffect, useState } from "react";
import IconButton from "@mui/material/IconButton";
import ShareIcon from "@mui/icons-material/Share";
import DownloadIcon from "@mui/icons-material/Download";
import Grid from "@mui/material/Grid";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { Box, Typography } from "@mui/material";
import { getAllImages } from "../../features/images.reducers";

export default function Image() {
  const [currentImage, setCurrentImage] = useState(0);
  const user = useSelector((state) => state.user.user);

  const dispatch = useDispatch();
  const images = useSelector((state) => state.images.images);

  // const [images, setData] = React.useState([]);
  const handlePrevious = () => {
    if (currentImage > 0) {
      setCurrentImage(currentImage - 1);
    }
  };

  const handleNext = () => {
    if (currentImage < images?.length - 1) {
      setCurrentImage(currentImage + 1);
    }
  };
  function generateCode() {
    let code = "";
    for (let i = 0; i < 4; i++) {
      code += Math.floor(Math.random() * 10);
    }
    return code;
  }

  const handleDownload = () => {
    const link = document.createElement("a");
    // link.href = images?.[currentImage]?.picture;
    link.href = images?.filter((el) => {
      if (el.category === "All" && user.partition === el.partition) return true;
      if (el.category === "All" && el.partition === "All") return true;
      if (
        user.names.toLowerCase().includes(el.category.toLowerCase()) &&
        (el.partition === user.partition || el.partition === "All")
      )
        return true;
      else return false;
    })[currentImage]?.picture;

    const date = new Date(); // crée un objet Date avec la date et l'heure actuelles
    const jour = String(date.getDate()).padStart(2, "0"); // extrait le jour et ajoute un zéro devant si le nombre n'a qu'un seul chiffre
    const mois = String(date.getMonth() + 1).padStart(2, "0"); // extrait le mois (en commençant par 0 pour janvier) et ajoute un zéro devant si le nombre n'a qu'un seul chiffre
    const annee = date.getFullYear(); // extrait l'année

    const dateFormatee = `${annee}${mois}${jour}`; // combine les valeurs pour créer la chaîne de date et heure formatée

    link.download = `IMG-${dateFormatee}-LGP-${generateCode()}.jpg`;
    link.click();
  };

  const Voyelle = ["A", "O", "U", "E", "I"];

  const handleShare = () => {
    const fistWord = user?.names.charAt(0).toLowerCase();
    navigator.share({
      title: `Image Fanfare La Grâce Parle`,
      text: Voyelle.includes(fistWord)
        ? `Visite ce profil d'${user.names} du groupe Fanfare La Grâce Parle `
        : `Visite ce profil de ${user.names} du groupe Fanfare La Grâce Parle `,
      // url: images?.[currentImage]?.picture,
      url: images?.filter((el) => {
        if (el.category === "All" && user.partition === el.partition)
          return true;
        if (el.category === "All" && el.partition === "All") return true;
        if (
          user.names.toLowerCase().includes(el.category.toLowerCase()) &&
          (el.partition === user.partition || el.partition === "All")
        )
          return true;
        else return false;
      })[currentImage]?.picture,
    });
  };
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_PICTURE}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };

  useEffect(() => {
    sendRquestPost().then((data) => {
      // setData(data);
      dispatch(getAllImages(data));
    });
  }, [dispatch]);

  return (
    <Box>
      {images.length >= 1 ? (
        <>
          <Grid container spacing={1} alignItems="center">
            {/* <Grid item>
              <IconButton
                onClick={handlePrevious}
                disabled={currentImage === 0}
              >
                {"<"}
              </IconButton>
            </Grid> */}
            <Grid item xs>
              <img
                // src={images?.[currentImage]?.picture}
                src={
                  images?.filter((el) => {
                    if (
                      el.category === "All" &&
                      user.partition === el.partition
                    )
                      return true;
                    if (el.category === "All" && el.partition === "All")
                      return true;
                    if (
                      user.names
                        .toLowerCase()
                        .includes(el.category.toLowerCase()) &&
                      (el.partition === user.partition ||
                        el.partition === "All")
                    )
                      return true;
                    else return false;
                  })[currentImage]?.picture
                }
                alt="slider"
                style={{ width: "100%", height: "600px", objectFit: "cover" }}
              />
              {/* {images
  .filter((el) => {
    if (
      user.names.toLocaleLowerCase().includes(el.category.toLocaleLowerCase()) &&
      user.partition === el.partition
    ) {
      return true;
    }
    if (el.category === "All" && el.partition === user.partition) {
      return true;
    }
    if (el.category === "All" && el.partition === "All") {
      return true;
    }
    return false; // Ajout de ce return pour être sûr que la condition retourne un booléen
  })
  .some((img) => img === images[currentImage]) && (
    <Grid item xs>
      <img
        src={images?.[currentImage]?.picture}
        alt="slider"
        style={{ width: "100%", height: "auto", objectFit: "cover" }}
      />
    </Grid>
  )} */}
            </Grid>

            {/* <Grid item>
              <IconButton
                onClick={handleNext}
                disabled={currentImage === images?.length - 1}
              >
                {">"}
              </IconButton>
            </Grid> */}
          </Grid>
          <IconButton onClick={handleDownload}>
            <DownloadIcon />
          </IconButton>
          <IconButton onClick={handleShare}>
            <ShareIcon />
          </IconButton>
          {/* <Button onClick={handleDownload}>Télécharger</Button>
      <Button onClick={handleShare}>Partager</Button> */}
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              flexWrap: "wrap",
            }}
          >
            {images
              ?.filter((el) => {
                if (el.category === "All" && user.partition === el.partition)
                  return true;
                if (el.category === "All" && el.partition === "All")
                  return true;
                if (
                  user.names
                    .toLowerCase()
                    .includes(el.category.toLowerCase()) &&
                  (el.partition === user.partition || el.partition === "All")
                )
                  return true;
                else return false;
              })
              .map((img, index) => (
                <IconButton
                  key={index}
                  onClick={() => setCurrentImage(index)}
                  disabled={index === currentImage}
                >
                  <img
                    src={img.picture}
                    alt={`slider ${index}`}
                    style={{
                      width: 60,
                      height: 60,
                      objectFit: "cover",
                      border:
                        index === currentImage ? "1px solid gray" : "none",
                    }}
                  />
                </IconButton>
              ))}
          </div>
        </>
      ) : (
        <Typography
          sx={{ textAlign: "center", textTransform: "uppercase" }}
          variant="subtitle2"
        >
          Aucune image disponible
        </Typography>
      )}
    </Box>
  );
}
