import * as React from "react";
import { useState } from "react";
import { styled } from "@mui/system";
import { AppBar, IconButton, Button, Menu, MenuItem } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import { NavLink } from "react-router-dom";

const Navbar = styled(AppBar)({
  backgroundColor: "#DD9B01",
  boxShadow: "none",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "flex-end",
  flexWrap: "wrap",
  padding: "10px 20px",
  "& a": {
    color: "#fff",
    marginLeft: 20,
    fontSize: 16,
    textDecoration: "none",
    "&:hover": {
      color: "#ccc",
    },
  },
});

const NavbarMenu = styled("div")(({ theme }) => ({
  [theme.breakpoints.down("sm")]: {
    display: "none",
  },
  display: "flex",
  alignItems: "center",
  "& a": {
    marginLeft: 20,
  },
}));

const NavbarBurger = styled("div")(({ theme }) => ({
  [theme.breakpoints.up("md")]: {
    display: "none",
  },
  display: "flex",
  flexDirection: "column",
}));

const NavbarBurgerButton = styled(IconButton)({
  color: "#fff",
});

const NavbarBurgerMenu = styled(Menu)({
  "& a": {
    color: "#222",
    padding: "10px 20px",
    textDecoration: "none",
    display: "block",
    "&:hover": {
      backgroundColor: "#ccc",
    },
  },
});

const NavbarItems = ({ items }) => (
  <NavbarMenu>
    {items.map((item) => (
      <Button component={NavLink} to={item.href}>
        {item.text}
      </Button>
    ))}
  </NavbarMenu>
);

const NavbarBurgerItems = ({ items, anchorEl, handleClose }) => (
  <NavbarBurgerMenu
    anchorEl={anchorEl}
    open={Boolean(anchorEl)}
    onClose={handleClose}
  >
    {items.map((item) => (
      <MenuItem onClick={handleClose} key={item.text}>
        <Button component={NavLink} to={item.href}>
          {item.text}
        </Button>
      </MenuItem>
    ))}
  </NavbarBurgerMenu>
);

// const NavbarBrand = ({ text }) => (
//   <Typography variant="h6">
//     <a href="/">{text}</a>
//   </Typography>
// );

const NavbarBurgerButtonHandler = ({ handleClick }) => (
  <NavbarBurgerButton onClick={handleClick} aria-label="menu">
    <MenuIcon />
  </NavbarBurgerButton>
);

const NavbarContainer = ({ brand, items }) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Navbar position="static">
      {/* <NavbarBrand text={brand} /> */}
      <NavbarBurger>
        <NavbarBurgerButtonHandler handleClick={handleOpen} />
        <NavbarBurgerItems
          items={items}
          anchorEl={anchorEl}
          handleClose={handleClose}
        />
      </NavbarBurger>
      <NavbarItems items={items} />
    </Navbar>
  );
};

export default NavbarContainer;
