import React from "react";
import { Button, Stack } from "@mui/material";
import { useLocation, useNavigate } from "react-router-dom";

// const useStyles = makeStyles((theme) => ({
//   navbar: {
//     display: 'flex',
//     justifyContent: 'center',
//   },
// }));

function Test() {
  // const classes = useStyles();
  const history = useNavigate();

  const location = useLocation();
  return (
    <Stack
      spacing={3}
      sx={{
        display: "flex",
        justifyContent: "space-evenly",
        alignItems: "center",
        alignContent: "center",
        flexDirection: "row",
      }}
    >
      <Button
        onClick={() => {
          history("/gallery");
        }}
        variant="contained"
        sx={{
          marginTop: "25px",
          backgroundColor:
            location.pathname === "/gallery" ? "#C15500" : "skyblue",
        }}
        size="small"
      >
        Image
      </Button>

      <Button
        onClick={() => {
          history("/gallery");
        }}
        variant="contained"
        sx={{
          marginTop: "25px",
          backgroundColor:
            location.pathname === "/audio" ? "#C15500" : "skyblue",
        }}
        size="small"
      >
        Audio
      </Button>

      <Button
        onClick={() => {
          history("/gallery");
        }}
        variant="contained"
        sx={{
          marginTop: "25px",
          backgroundColor:
            location.pathname === "/video" ? "#C15500" : "skyblue",
        }}
        size="small"
      >
        Vidéo
      </Button>
    </Stack>
  );
}

export default Test;
