import {
  Alert,
  Button,
  CircularProgress,
  FormControl,
  Input,
  InputAdornment,
  InputLabel,
  Snackbar,
  Stack,
  Table,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setGetUser } from "../../../features/user.reducers";
import { setgetAll } from "../../../features/users.reducers";
import Navbar from "../NavbarAdmin";
import BasicTable from "./Table";
import { getUserWithVerifyToken, sendRquest } from "../../../app/verify";
// eslint-disable-next-line no-unused-vars
import { dateParser } from "../../../utils/dateParser";

const Parent = () => {
  const users = useSelector((state) => state.users.users);
  const user = useSelector((state) => state.user.user);

  const dispatch = useDispatch();
  const [searchValue, setSearchValue] = useState("");
  const [send, setSend] = useState(false);
  const [sendMessage, setSendMessage] = useState(false);
  const [error, setError] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [loadingTwo, setLoadingTwo] = React.useState(false);
  const [loadingThree, setLoadingThree] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const SendPdfListe = async () => {
    setLoading(true);
    await axios
      .get(`${process.env.REACT_APP_URL_USER}/liste/${user._id}`)
      .then((res) => {
        // console.log(res)
        setLoading(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(res.data.message);
      })
      .catch((err) => {
        setLoading(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };

  const onResetListe = async () => {
    setLoadingTwo(true);
    await axios
      .put(`${process.env.REACT_APP_URL_USER}/reset`)
      .then((res) => {
        setLoadingTwo(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(res.data.message);
      })
      .catch((err) => {
        // console.log(err)    setLoading(false);
        setLoadingTwo(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };
  const onResetListeAll = async () => {
    setLoadingThree(true);
    await axios
      .put(`${process.env.REACT_APP_URL_USER}/resetAll`)
      .then((res) => {
        setLoadingThree(false);
        setOpen(true);
        setResToHome(true);
        setsuccees(res.data.message);
      })
      .catch((err) => {
        setLoadingThree(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };
  // eslint-disable-next-line array-callback-return
  const nombrePresent = users?.filter((el) => {
    if (el?.status === "A l'heure") return true;
  });
  // eslint-disable-next-line array-callback-return
  const nombreRetard = users?.filter((el) => {
    if (el?.status === "En retard") return true;
  });
  // eslint-disable-next-line array-callback-return
  const nombreAbsent = users?.filter((el) => {
    if (el?.status === "Absent") return true;
  });
  const now = new Date(); // Récupérez la date et l'heure actuelle
  function formatDate(date) {
    const day = date.getDate().toString().padStart(2, "0");
    const month = (date.getMonth() + 1).toString().padStart(2, "0");
    const year = date.getFullYear().toString();

    return `${day}/${month}/${year}`;
  }
  useEffect(() => {
    getUserWithVerifyToken().then((data) => {
      dispatch(setGetUser(data.user));
    });
  }, [dispatch]);
  useEffect(() => {
    sendRquest().then((data) => {
      dispatch(setgetAll(data));
    });
  }, [dispatch]);
  return (
    <>
      {(user?.isAdmin || user.isAdminDev) && (
        <div>
          {resToHome ? (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="success"
                  sx={{ width: "100%" }}
                >
                  {succees}
                </Alert>
              </Snackbar>
            </Stack>
          ) : (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="error"
                  sx={{ width: "100%" }}
                >
                  {error}
                </Alert>
              </Snackbar>
            </Stack>
          )}
          <Navbar />
          <Typography>Rechercher votre code </Typography>
          <FormControl fullWidth sx={{ m: 1 }} variant="standard">
            <InputLabel htmlFor="standard-adornment-amount">
              Votre Code Personnel
            </InputLabel>
            <Input
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
              id="standard-adornment-amount"
              startAdornment={
                <InputAdornment position="start">code</InputAdornment>
              }
            />
          </FormControl>

          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Nom</TableCell>
                <TableCell align="left">Prénom</TableCell>
                <TableCell align="left">Code</TableCell>
                <TableCell align="center">Statut</TableCell>
                <TableCell align="right">heure</TableCell>
              </TableRow>
            </TableHead>
          </Table>

          {users
            ?.filter(
              (item) =>
                item.identification
                  ?.toLowerCase()
                  .includes(searchValue.toLowerCase()) &&
                item.isSuperAdmin !== true
            )
            .map((el) => {
              return <BasicTable key={el?._id} el={el} />;
            })}
          <br></br>
          <>
            <Typography variant="subtitle2">
              {nombrePresent.length} sont vénu à l'heure au répétition sur{" "}
              {users?.length - 3}{" "}
            </Typography>

            <Typography variant="subtitle2">
              {nombreRetard.length} sont vénu en rétard au répétition{" "}
            </Typography>
            <Typography variant="subtitle2">
              {nombreAbsent.length} absent au répétition{" "}
            </Typography>
            <Typography variant="subtitle2">
              {nombrePresent.length + nombreRetard.length} sont venu au répète
              du {formatDate(now)}
            </Typography>
          </>
          {loading ? (
            <CircularProgress />
          ) : (
            <Button
              type="submit"
              onClick={SendPdfListe}
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Envoyer le fichier
            </Button>
          )}
          <br />
          <br />
          {loadingTwo ? (
            <CircularProgress />
          ) : (
            <Button
              type="submit"
              onClick={onResetListe}
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Cloturer la liste
            </Button>
          )}
          <br />
          <br />
          {loadingThree ? (
            <CircularProgress />
          ) : (
            <Button
              type="submit"
              onClick={onResetListeAll}
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Remetrre à zéro la liste
            </Button>
          )}
        </div>
      )}
    </>
  );
};

export default Parent;
