import { configureStore } from "@reduxjs/toolkit";
import userReducer from "../features/user.reducers";
import partitionReducer from "../features/partition.reducers";

import AudioReducer from "../features/audio.reducers";

import VideoReducer from "../features/video.reducers";

import usersReducers from "../features/users.reducers";
import calendarsReducers from "../features/calendar.reducers";
import exercicesReducers from "../features/exercices.reducers";
import imagesReducers from "../features/images.reducers";

export default configureStore({
  reducer: {
    user: userReducer, // Reducer de de l'utilisateur
    partitions: partitionReducer, //Reducer des partitions
    exercices: exercicesReducers, //Reducer des partitions
    audios: AudioReducer, //Reducer des partitions
    videos: VideoReducer, //Reducer des partitions
    users: usersReducers, // Reducer de touts les utilisateurs
    calendars: calendarsReducers, // Reducer des calendriers
    images: imagesReducers, // Reducer des images
  },
});
