import { createSlice } from "@reduxjs/toolkit";

export const audioslice = createSlice({
  name: "audios",
  initialState: {
    audios: [],
  },
  reducers: {
    /**Récuperer touts les audios */
    getAllaudio: (state, { payload }) => {
      state.audios = payload;
    },
    /**Ajouter une audio */
    addaudio: (state, { payload }) => {
      state.audios?.push(payload);
    },
  },
});

export const { getAllaudio, addaudio } = audioslice.actions;
export default audioslice.reducer;
