import * as React from "react";
import Card from "@mui/material/Card";

import Typography from "@mui/material/Typography";

import axios from "axios";
import { Button, Stack } from "@mui/material";

export default function Users({ el }) {
  const handleDelete = async () => {
    axios
      .delete(`${process.env.REACT_APP_URL_USER}/delete/${el._id}`)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => alert(err.response.data.message));
  };

  const handleSouscription = async () => {
    axios
      .post(`${process.env.REACT_APP_URL_USER}/souscription/${el._id}`)
      .then((res) => alert(res.data.message))
      .catch((err) => alert(err.response.data.message));
  };

  return (
    <>
      <Card
        sx={{
          width: "90mm",
          height: "85mm",
          alignItems: "center",
          margin: "auto",
          position: "relative",
          // background: "linear-gradient(to right, green, yellow,red)",
        }}
      >
        <Typography
          align="center"
          variant="subtitle1"
          textTransform="uppercase"
        >
          {" "}
          Republique du Bénin
        </Typography>
        <Typography align="center" variant="body2" textTransform="uppercase">
          {" "}
          Carte numérique d'identification Peronnel
        </Typography>
        <Typography
          align="center"
          variant="body1"
          color="secondary"
          textTransform="uppercase"
        >
          {" "}
          N° LGP{el?.identification}
        </Typography>
        {/* <img
          style={{ position: "absolute", top: "3%", left: "3%", width: "30px" }}
          src="logo.jpg" alt="logo grâce qui parle"
        /> */}
        <img
          style={{
            position: "absolute",
            top: "30%",
            right: "3%",
            width: "40px",
          }}
          src={el?.picture}
          alt="image_personnel"
        />
        <img
          style={{
            position: "absolute",
            bottom: "7%",
            right: "2%",
            width: "85px",
          }}
          src={el?.qrCode}
          alt="image_personnel"
        />
        <Stack direction="row" spacing={5}>
          <div>
            <Typography fontWeight="bolder" variant="subtitle2">
              id
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Nom
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Prenom
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Instrument
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Partition
            </Typography>
            {el?.sexe && (
              <Typography fontWeight="bolder" variant="subtitle2">
                Sexe
              </Typography>
            )}

            <Typography fontWeight="bolder" variant="subtitle2">
              Groupe Sanguin
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Tel
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              statut
            </Typography>
          </div>
          <div>
            <Typography variant="subtitle2"> {el?._id}</Typography>
            <Typography variant="subtitle2">{el?.firstName}</Typography>
            <Typography variant="subtitle2">{el?.lastName}</Typography>
            <Typography variant="subtitle2">{el?.instrument}</Typography>
            <Typography variant="subtitle2">{el?.partition}</Typography>
            {el?.sexe && (
              <Typography variant="subtitle2">{el?.sexe}</Typography>
            )}

            <Typography variant="subtitle2">{el?.sanguin}</Typography>
            <Typography variant="subtitle2">+229 {el?.tel}</Typography>
            <Typography variant="subtitle2"> {el?.idVerified}</Typography>
          </div>
        </Stack>
        <Button
          onClick={() => {
            if (
              window.confirm(
                "Voulez vous vraiment vous supprimez ce utilisateur ?"
              )
            ) {
              // eslint-disable-next-line no-lone-blocks
              {
                handleDelete();
              }
            }
          }}
        >
          Supprimer l'utilisateur
        </Button>
        <Button onClick={handleSouscription}>Ne me cliquer pas</Button>
      </Card>
      <br></br>
    </>
  );
}
