import React from "react";
import { Icon, Typography } from "@mui/material";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { NavLink } from "react-router-dom";

const Condition = () => {
  return (
    <div>
      <NavLink to="/register">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>

      <Typography variant="h6" sx={{ textAlign: "center" }}>
        conditions générales
      </Typography>

      <Typography variant="h6">Consultation des morceaux</Typography>
      <Typography variant="subtitle2">
        Les utilisateurs pourront consulter la liste des morceaux joués par le
        groupe de fanfare, ainsi que les informations relatives à chaque morceau
        (compositeur, année de création, style musical, etc.).
      </Typography>
      <Typography variant="h6">Frias de souscription</Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        : Les utilisateurs pourront payer les frais d'adhésion, les frais de
        participation aux événements et les autres frais liés au fonctionnement
        du groupe de fanfare via l'application. Les paiements se feront de
        manière sécurisée via des prestataires de services de paiement tiers.
      </Typography>
      <Typography variant="h6">Liste de présence</Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        {" "}
        L'application permettra aux membres du groupe de fanfare de prendre la
        liste de présence lors des répétitions et des concerts. Les utilisateurs
        pourront également consulter la liste de présence des membres pour
        chaque événement.
      </Typography>
      <Typography variant="h6">Evaluation des performances</Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        Les utilisateurs pourront évaluer les performances du groupe de fanfare
        lors des concerts et des événements. Les membres du groupe de fanfare
        pourront également consulter les évaluations et les commentaires des
        utilisateurs pour améliorer leur performance.
      </Typography>
      <Typography variant="h6">Confidentialite des données</Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        Le groupe de fanfare s'engage à respecter la confidentialité des données
        personnelles des utilisateurs de l'application, conformément aux lois et
        règlements en vigueur. Les données personnelles des utilisateurs ne
        seront pas vendues ou partagées avec des tiers sans leur consentement
        préalable.
      </Typography>
      <Typography variant="h6">Condition d'utilisation</Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        Les utilisateurs de l'application devront accepter les conditions
        d'utilisation, qui préciseront les règles de fonctionnement de
        l'application et les responsabilités des utilisateurs. Le groupe de
        fanfare se réserve le droit de supprimer les utilisateurs qui ne
        respectent pas les conditions d'utilisation de l'application.
      </Typography>
      <Typography variant="h6">Contactez-nous</Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        Si vous avez des questions ou des préoccupations concernant nos
        conditions d'utilisation, veuillez nous écrire sur whatsApp.
      </Typography>
      <Typography variant="subtitle2" sx={{ textAlign: "justify" }}>
        Dernière mise à jour : 17/03/2023.
      </Typography>

      <br></br>
    </div>
  );
};

export default Condition;
