import React, { useEffect, useState } from "react";
import { Button, Grid, TextField } from "@mui/material";
import { styled } from "@mui/material/styles";
import { useDispatch } from "react-redux";
import axios from "axios";
import {
  addExercices,
  getAllExercices,
} from "../../../features/exercices.reducers";

const PostFormContainer = styled(Grid)(({ theme }) => ({
  "& .MuiTextField-root": {
    margin: theme.spacing(1),
  },
}));

function PostForm(props) {
  const dispatch = useDispatch();

  const [message, setmessage] = useState("");

  const sendRquest = async () => {
    const res = await axios
      .get(`${process.env.REACT_APP_URL_EXERCICES}`, {
        withCredentials: true,
      })
      .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };

  const handlePostUpload = async () => {
    if (message) {
      const res = await axios.post(`${process.env.REACT_APP_URL_EXERCICES}`, {
        message,
      });

      const datas = await res.data;
      return datas;
    } else {
      alert("Veuillez entrez un méssage");
    }
  };
  const handlePost = async (e) => {
    handlePostUpload()
      .then((datas) => {
        dispatch(addExercices(datas));
        alert(datas.message);
      })
      .then(() => {
        dispatch(getAllExercices());
      })
      .catch((err) => alert(err.response.data.message));
    cancelPost();
  };

  const cancelPost = () => {
    setmessage("");
  };

  useEffect(() => {
    sendRquest().then((data) => {
      dispatch(getAllExercices(data));
    });
  }, [dispatch]);

  return (
    <>
      <PostFormContainer container spacing={2}>
        <Grid item xs={12}>
          <TextField
            label="Message"
            multiline
            name="message"
            rows={4}
            fullWidth
            value={message}
            onChange={(event) => setmessage(event.target.value)}
          />
        </Grid>

        <Grid item xs={12}>
          <Button
            type="submit"
            onClick={handlePost}
            variant="contained"
            color="primary"
          >
            Envoyer
          </Button>
        </Grid>
      </PostFormContainer>
    </>
  );
}

export default PostForm;
