import React from "react";

import { useDispatch } from "react-redux";

import { useEffect } from "react";
import axios from "axios";
import { getAllaudio } from "../../features/audio.reducers";

import { Typography, Icon, Stack, Button } from "@mui/material";

import { getAllvideo } from "../../features/video.reducers";

import { NavLink, useLocation, useNavigate } from "react-router-dom";

import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import Image from "./Image";

const HomeGallery = () => {
  /**State global */
  const dispatch = useDispatch();
  const history = useNavigate();

  /**Récuperer touts nos audios */
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_AUDIO}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };
  const sendRquestPostVideos = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_VIDEO}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };
  const location = useLocation();

  useEffect(() => {
    sendRquestPost().then((data) => {
      dispatch(getAllaudio(data));
    });
  }, [dispatch]);
  useEffect(() => {
    sendRquestPostVideos().then((data) => {
      dispatch(getAllvideo(data));
    });
  }, [dispatch]);
  return (
    <div>
      {/* <Navbar/> */}
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      <br />
      <br />
      <Stack
        spacing={3}
        sx={{
          display: "flex",
          justifyContent: "space-evenly",
          alignItems: "center",
          alignContent: "center",
          flexDirection: "row",
        }}
      >
        <Button
          onClick={() => {
            history("/gallery");
          }}
          variant="contained"
          sx={{
            marginTop: "25px",
            backgroundColor:
              location.pathname === "/gallery" ? "#C15500" : "skyblue",
          }}
          size="small"
        >
          Image
        </Button>

        <Button
          onClick={() => {
            history("/audio");
          }}
          variant="contained"
          sx={{
            marginTop: "25px",
            backgroundColor:
              location.pathname === "/audio" ? "#C15500" : "skyblue",
          }}
          size="small"
        >
          Les Audios
        </Button>

        <Button
          onClick={() => {
            history("/video");
          }}
          variant="contained"
          sx={{
            marginTop: "25px",
            backgroundColor:
              location.pathname === "/video" ? "#C15500" : "skyblue",
          }}
          size="small"
        >
          Les Vidéos
        </Button>
      </Stack>
      <br />
      <br />
      <h2 style={{ textAlign: "center", color: "#C15500" }}>
        Toutes vos images d'évènements
      </h2>

      <Image />
    </div>
  );
};

export default HomeGallery;
