import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import VisibilityIcon from "@mui/icons-material/Visibility";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import PasswordIcon from "@mui/icons-material/Password";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Visibility from "@mui/icons-material/Visibility";

import axios from "axios";
import {
  Alert,
  FormControl,
  InputAdornment,
  Snackbar,
  Stack,
} from "@mui/material";
import CircularProgress from "@mui/material/CircularProgress";

import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setGetUser } from "../../features/user.reducers";

// eslint-disable-next-line no-unused-vars
function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright © "}
      <Link color="inherit" href="">
        FanFare GQP
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const theme = createTheme();
export default function SignInSide() {
  const dispatch = useDispatch();
  const [error, setError] = React.useState("");

  const [showPassword, setShowPassword] = React.useState(false);
  const history = useNavigate();
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);

    if (!navigator.onLine) {
      setOpen(true);
      setError("Si la connexion n'est pas active, vérifiez la connexion");
      setLoading(false);
    }

    const data = new FormData(event.currentTarget);
    // console.log({
    //   identifier: data.get("email"),
    //   password: data.get("password"),
    // });
    axios
      .post(`${process.env.REACT_APP_URL_USER}/login`, {
        identifier: data.get("email"),
        password: data.get("password"),
      })
      .then((res) => {
        // console.log(res.data.message);
        setLoading(true);
        setResToHome(true);
        setOpen(true);
        setsuccees(res.data.message);

        setTimeout(() => {
          history("/home");
        }, 2000);
      })
      .catch((error) => {
        setLoading(false);
        setOpen(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };
  const sendRquest = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };
  React.useEffect(() => {
    sendRquest().then((data) => {
      dispatch(setGetUser(data.user));
      setTimeout(() => {
        history("/home");
      });
    });
  }, [dispatch]);

  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: "100vh" }}>
        <CssBaseline />
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url('logo.jpg')`,
            objectFit: "cover",
            backgroundRepeat: "no-repeat",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}></Avatar>

            <Typography component="h1" variant="h6" textTransform="uppercase">
              Se connecter
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email ou Numéro de téléphone"
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="Mot de passe"
                // type="password"
                id="password"
                autoComplete="current-password"
                endIcon={<VisibilityIcon />}
                type={showPassword ? "text" : "password"}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PasswordIcon />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                      sx={{ cursor: "pointer" }}
                      position="end"
                    >
                      {" "}
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </InputAdornment>
                  ),
                }}
              />

              <FormControl
                sx={{ m: 1, width: "25ch" }}
                variant="filled"
              ></FormControl>

              {loading ? (
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <CircularProgress />
                </Box>
              ) : (
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{
                    // textTransform:'lowercase',
                    mt: 3,
                    mb: 2,
                    background: {
                      md: "#DD9B01",
                      xs: "#DD9B01",
                      "&:hover": {
                        background: "#6D4826",
                      },
                    },
                  }}
                >
                  Se connecter
                </Button>
              )}

              <Grid
                container
                style={{
                  diplay: "flex",
                  flexDirection: "column",
                  alignItem: "center",
                  alignContent: "center",
                  justifyContent: "center",
                }}
              >
                <Grid item xs>
                  <Button
                    sx={{ textDecoration: "underline" }}
                    variant="text"
                    fullWidth
                    onClick={() => {
                      history("/forget");
                    }}
                    size="small"
                  >
                    Mot de passe oublié ?
                  </Button>
                </Grid>

                <hr
                  style={{
                    width: "100%",
                    height: "3.5px",
                    backgroundColor: "gray",
                  }}
                ></hr>

                <Grid item xs>
                  <Button
                    fullWidth
                    variant="contained"
                    sx={{
                      color: "uppercase",
                      background: {
                        md: "skyblue",
                        xs: "skyblue",
                        "&:hover": {
                          background: "blue",
                        },
                      },
                      marginTop: "25px",
                    }}
                    onClick={() => {
                      history("/register");
                    }}
                    size="small"
                  >
                    Créer nouveau compte
                  </Button>
                </Grid>
              </Grid>

              {/* <Copyright sx={{ mt: 5 }} /> */}
            </Box>
          </Box>
        </Grid>
      </Grid>
      {resToHome ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={7000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
    </ThemeProvider>
  );
}
