import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

// import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";

import { NavLink } from "react-router-dom";
// eslint-disable-next-line no-unused-vars
import YoutubeSearchedForIcon from "@mui/icons-material/YoutubeSearchedFor";
import { useSelector } from "react-redux";
import NavbarContainer from "../Menu";

const pages = [
  {
    nom: "Presence",
    link: "/admin/home-liste",
  },
  {
    nom: "Accueil",
    link: "/home",
  },
];
const menuItems = [
  { href: "/admin/home-morceau", text: "Morçeaux" },
  { href: "/admin/home-users", text: "Utilisateurs" },
  { href: "/admin/home-participants", text: "Participants" },
  {
    href: "/exercices",
    text: "Communiquer",
  },
  { href: "/admin/home-liste", text: "Présence" },
  { href: "/img", text: "Les images" },
  { href: "/home", text: "Accueil" },
];

const pagesAdminPupitre = [
  {
    nom: "Evaluation",
    link: "/admin/home-note",
  },
  {
    nom: "Accueil",
    link: "/home",
  },
];

function Navbar() {
  const user = useSelector((state) => state.user?.user);

  return (
    <>
      {user.isAdmin && (
        <AppBar position="sticky" sx={{ backgroundColor: "#DB9A02" }}>
          <Container maxWidth="xl">
            <Toolbar disableGuttzers>
              {/* <AdbIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} /> */}
              <Box sx={{ flexGrow: 1, display: { xs: "flex" } }}>
                {pages.map((page) => (
                  <Typography
                    component={NavLink}
                    to={page.link}
                    key={page.link}
                    sx={{ marginRight: 1, color: "white", fontSize: "12px" }}
                    variant="outlined"
                  >
                    {<Typography>{page.nom}</Typography>}
                  </Typography>
                ))}
              </Box>
            </Toolbar>
          </Container>
        </AppBar>
      )}
      {user.isAdminDev && <NavbarContainer items={menuItems} />}

      {user.isAdminPupitre && (
        <AppBar position="sticky" sx={{ backgroundColor: "#DB9A02" }}>
          <Container maxWidth="xl">
            <Toolbar disableGutters>
              {/* <AdbIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} /> */}

              <Box sx={{ flexGrow: 1, display: { xs: "flex" } }}>
                {pagesAdminPupitre.map((page) => (
                  <Typography
                    component={NavLink}
                    to={page.link}
                    key={page.link}
                    sx={{ marginRight: 1, color: "white", fontSize: "12px" }}
                    variant="outlined"
                  >
                    {<Typography>{page.nom}</Typography>}
                  </Typography>
                ))}
              </Box>
            </Toolbar>
          </Container>
        </AppBar>
      )}
    </>
  );
}
export default Navbar;
