import { createSlice } from "@reduxjs/toolkit";

export const imagesSlice = createSlice({
  name: "images",
  initialState: {
    images: [],
  },
  reducers: {
    /**Récuperer touts les images */
    getAllImages: (state, { payload }) => {
      state.images = payload;
    },
    /**Ajouter une partition */
    addImages: (state, { payload }) => {
      state.images?.push(payload);
    },
    deleteImages: (state, { payload }) => {
      state.images = state.images.filter((post) => post._id !== payload);
    },
  },
});

export const { getAllImages, addImages, deleteImages } = imagesSlice.actions;
export default imagesSlice.reducer;
