import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import CameraAltIcon from "@mui/icons-material/CameraAlt";
import { useDispatch, useSelector } from "react-redux";
import {
  Alert,
  Box,
  CircularProgress,
  IconButton,
  Snackbar,
  Typography,
} from "@mui/material";
import { NavLink, useNavigate } from "react-router-dom";
import LogoutIcon from "@mui/icons-material/Logout";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import axios from "axios";
import { logOut, setgetUnique, uploadPicture } from "../features/user.reducers";

export default function ImageAvatars() {
  //State global
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user?.user);
  //state local
  const [file, setFile] = React.useState(null);
  const [error, setError] = React.useState("");
  const [open, setOpen] = React.useState(false);
  const [opens, setOpens] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [success, setSucces] = React.useState("");
  const history = useNavigate();
  /**Mui function  */
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleCloses = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpens(false);
  };

  //LogOut function
  const sendlogOutRequest = async () => {
    const res = await axios.post(
      `${process.env.REACT_APP_URL_USER}/logout`,
      null,
      {
        withCredentials: true,
      }
    );
    if (res.status === 200) {
      return res;
    }
    return new Error("Déconnexion échoué, reprenez svp");
  };
  const handleLogOut = () => {
    sendlogOutRequest()
      .then(() => {
        setOpen(false);
        dispatch(logOut());
        history("/");
      })
      .catch((error) => {
        setOpen(false);
        setOpens(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };
  //Upload function

  const handlePicture = async (e) => {
    const data = new FormData();
    data.append("name", user.names);
    data.append("userId", user._id);
    data.append("user", file);
    // Effectuez une requête POST via Axios
    const sizelimit = 7 * 1024 * 1024; // Fichier de 7 mo
    if (file?.size > sizelimit) {
      setOpens(true);
      setFile(null);
      setLoading(false);
      return setError(
        `La taille du fichier ne devrait pas dépasser 7 mo, veuillez en choisir un autre svp`
      );
    }
    const res = await axios
      .post(`${process.env.REACT_APP_URL_USER}/upload`, data, {
        withCredentials: true,
      })
      .then((response) => {
        console.log(response);
        setFile(null);
        setOpens(true);
        setSucces(response.data.message);
        setLoading(false);
        axios.get(`${process.env.REACT_APP_URL_USER}/jwt`);
      })
      .catch((error) => {
        console.log(error);
        setOpens(true);
        if (error.response.data.message) setError(error.response.data.message);
        else setError("Veuillez réessayer plus tard");
      });
    const datas = await res.data;
    return datas;
  };
  const uploadPictures = async (e) => {
    e.preventDefault();
    setLoading(true);

    handlePicture().then((datas) => {
      setLoading(false);
      dispatch(uploadPicture(datas));
    });
  };
  /**Souscription */

  return (
    <>
      <Button style={{ color: "white" }}>LGP PHILHARMONIA</Button>

      {/* {user?.isAdmin && (
        <NavLink to="/admin/home-liste">
          <Button>Administrateur</Button>
        </NavLink>
      )} */}
      {user?.isAdminDev && (
        <NavLink to="/admin/home-liste">
          <Button style={{ textDecoration: "underline", color: "white" }}>
            Admin page
          </Button>
        </NavLink>
      )}

      {user?.isAdminPupitre && (
        <NavLink to="/admin/home-note">
          <Button style={{ textDecoration: "underline", color: "white" }}>
            CP page
          </Button>
        </NavLink>
      )}
      {/* {user?.isAdminDev && (
        <NavLink to="/exercices">
          <Button style={{ textDecoration: "underline", color: "white" }}>
            Faire une annonce
          </Button>
        </NavLink>
      )} */}
      <div style={{ position: "absolute", top: "5px", right: "0px" }}>
        <Button size="small" onClick={handleClickOpen}>
          <IconButton>
            <LogoutIcon size="small" sx={{ color: "red" }} />
          </IconButton>
        </Button>
        <Dialog
          open={open}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{"Déconnexion"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              Etes vous surs de vouloir vous deconnecter ?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button sx={{ color: "red" }} onClick={handleClose}>
              Non
            </Button>
            <Button sx={{ color: "red" }} onClick={handleLogOut} autoFocus>
              Oui
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      {/* <Button variant="text" size="small">
        <a
          style={{ textDecoration: "none" }}
          href="https://tubular-pixie-70fab0.netlify.app/"
          target="blank"
        >
          Quizz Game
        </a>
      </Button> */}
      <Stack
        direction="row"
        sx={{
          alignItems: "center",
          justifyContent: "space-between",
        }}
        spacing={2}
      >
        <form onSubmit={uploadPictures}>
          <div className="upload">
            <Button
              sx={{
                position: "relative",
                padding: "11px 16px",
                fontSize: "15px",
                lineHeight: "1.5",
                borderRadius: "3px",
                border: "0",
                overflow: "hidden",
              }}
            >
              <Avatar
                sx={{ cursor: "pointer", fontSize: "3rem" }}
                alt={user?.names}
                // src={user.picture}
                src={user?.picture}
                size="large"
              />
              <input
                accept=".jpg, .jpeg, .png"
                style={{
                  cursor: "pointer",
                  position: "absolute",
                  left: "0%",
                  top: "0%",
                  transform: "scale(5)",
                  opacity: "0",
                }}
                onChange={(e) => {
                  setFile(e.target.files[0]);
                }}
                type="file"
              />

              <IconButton size="large">
                <CameraAltIcon />
              </IconButton>
            </Button>
            {file !== null && loading === false ? (
              <Button
                variant="contained"
                size="small"
                type="submit"
                sx={{ position: "absolute", left: "6.5rem", top: "2.5rem" }}
              >
                Télécharger l'image
              </Button>
            ) : (
              ""
            )}
            {loading === true ? (
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <CircularProgress />
              </Box>
            ) : (
              ""
            )}

            {user.souscription === "" && (
              <Button variant="contained" size="small">
                <a
                  style={{ textDecoration: "none" }}
                  href="https://me.fedapay.com/souscription_1234"
                  target="blank"
                >
                  Souscrire
                </a>
              </Button>
            )}
          </div>
        </form>
        {/* <div  > 
       
      </div> */}
      </Stack>
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={opens} autoHideDuration={10000} onClose={handleCloses}>
          <Alert onClose={handleCloses} severity="error" sx={{ width: "100%" }}>
            {error}
          </Alert>
        </Snackbar>
        <Snackbar open={opens} autoHideDuration={10000} onClose={handleCloses}>
          <Alert
            onClose={handleCloses}
            severity="success"
            sx={{ width: "100%" }}
          >
            {success}
          </Alert>
        </Snackbar>
      </Stack>
    </>
  );
}
