import { createSlice } from "@reduxjs/toolkit";

export const exerciceslice = createSlice({
  name: "exercices",
  initialState: {
    exercices: [],
  },
  reducers: {
    getAllExercices: (state, { payload }) => {
      state.exercices = payload;
    },

    addExercices: (state, { payload }) => {
      state.exercices?.push(payload);
    },

    deleteExercices: (state, { payload }) => {
      state.exercices = state.exercices.filter((post) => post._id !== payload);
    },
    // deleteUser: (state, { payload }) => {
    //   state.user = state.user.filter((user) => {
    //     return user.id !== payload;
    //   });
    // },
    likeExercices: (state, { payload }) => {
      state.exercices = state.exercices?.map((post) => {
        if (post._id === payload) {
          return state.exercices.likers?.push(payload);
        }
        return post;
      });
    },
    signalPost: (state, { payload }) => {
      state.exercices = state.exercices?.map((post) => {
        if (post._id === payload) {
          return state.exercices.signal?.push(payload);
        }
        return post;
      });
    },
    unsignalPost: (state, { payload }) => {
      state.exercices = state.exercices?.map((post) => {
        if (post._id === payload) {
          return state.exercices.signal?.filter((id) => id !== payload);
        }
        return post;
      });
    },
    dislikeExercices: (state, { payload }) => {
      state.exercices = state.exercices?.map((post) => {
        if (post._id === payload) {
          return state.exercices.likers?.filter((id) => id !== payload);
        }
        return post;
      });
    },

    editExercices: (state, { payload }) => {
      state.exercices = state.exercices.map((post) => {
        if (post._id === payload) {
          return state.exercices.message?.push(payload);
        } else return post;
      });
    },
    // exercices commentaires

    addComment: (state, { payload }) => {
      state.exercices = state.exercices?.map((post) => {
        if (post._id === payload) {
          return state.exercices.comments?.push(payload);
        }
        return post;
      });
    },
    addComments: (state, { payload }) => {
      state.exercices = state.exercices?.map((post) => {
        if (post._id === payload) {
          return state.exercices.comments?.push(payload);
        }
        return post;
      });
    },
    editCommentPost: (state, { payload }) => {
      state.exercices = state.exercices.map((post) => {
        if (post._id === payload) {
          return {
            ...post,
            comments: post.comments.map((comment) => {
              if (comment._id === payload) {
                return {
                  ...comment,
                  text: payload,
                };
              } else {
                return comment;
              }
            }),
          };
        } else return post;
      });
    },
    deleteCommentPost: (state, { payload }) => {
      state.exercices = state.exercices.map((post) => {
        if (post._id === payload) {
          return {
            ...post,
            comments: post.comments.filter(
              (comment) => comment._id !== payload
            ),
          };
        } else return post;
      });
    },
  },
});

export const {
  getAllExercices,
  editCommentPost,
  deleteCommentPost,
  addExercices,
  deleteExercices,
  likeExercices,
  signalPost,
  unsignalPost,
  dislikeExercices,
  editExercices,
  addComment,
} = exerciceslice.actions;
export default exerciceslice.reducer;
