import React from "react";
import { Button, Icon, Stack, Typography } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import axios from "axios";
import Music from "./Music";
import { getAllaudio } from "../../features/audio.reducers";

import { NavLink, useLocation, useNavigate } from "react-router-dom";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

const AudioPlayer = () => {
  /**State global */
  const dispatch = useDispatch();
  const audios = useSelector((state) => state.audios.audios);
  const location = useLocation();
  const history = useNavigate();

  /**Récuperer touts nos audios */
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_AUDIO}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };

  /**Au chargement de la page */

  useEffect(() => {
    sendRquestPost().then((data) => {
      dispatch(getAllaudio(data));
    });
  }, [dispatch]);
  return (
    <div
      style={{
        backgroundImage: `url('./assets/id.jpg')`,
        backgroundPosition: "center",
        backgroundSize: "cover",
        height: "100vh",
        width: "100%",
        backgroundColor:
          "rgba(128, 128, 128, 0.5)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
      }}
    >
      {/* <Navbar />
      <br /><br /> */}
      <NavLink to="/gallery">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>

      <Stack
        spacing={3}
        sx={{
          display: "flex",
          justifyContent: "space-evenly",
          alignItems: "center",
          alignContent: "center",
          flexDirection: "row",
        }}
      >
        <Button
          onClick={() => {
            history("/gallery");
          }}
          variant="contained"
          sx={{
            marginTop: "25px",
            backgroundColor:
              location.pathname === "/gallery" ? "#C15500" : "skyblue",
          }}
          size="small"
        >
          Image
        </Button>

        <Button
          onClick={() => {
            history("/audio");
          }}
          variant="contained"
          sx={{
            marginTop: "25px",
            backgroundColor:
              location.pathname === "/audio" ? "#C15500" : "skyblue",
          }}
          size="small"
        >
          Les Audios
        </Button>

        <Button
          onClick={() => {
            history("/video");
          }}
          variant="contained"
          sx={{
            marginTop: "25px",
            backgroundColor:
              location.pathname === "/video" ? "#C15500" : "skyblue",
          }}
          size="small"
        >
          Les Vidéos
        </Button>
      </Stack>
      <br />
      <br />
      <h2 style={{ textAlign: "center", color: "#C15500" }}>
        Le répertoire des audios
      </h2>
      <br />
      <br />
      <Stack
        direction="row"
        sx={{
          flexWrap: "wrap",
          dipspaly: "flex",
          justifyContent: "space-around",
        }}
      >
        <div>
          {audios.map((audio) => {
            return <Music audio={audio} />;
          })}
        </div>
      </Stack>
    </div>
  );
};

export default AudioPlayer;
