export default function VerticalLinearStepper() {
  const [activeStep, setActiveStep] = React.useState(0);
  const [activeStepsCartedeux, setActiveStepsCartedeux] = React.useState(0);
  const [loading, setLoading] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [error, setError] = React.useState("");
  const [succees, setSucees] = React.useState("");
  const [response, setResponse] = React.useState(false);
  const user = useSelector((state) => state.user.user);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const generatePdf = () => {
    setLoading(true);
    axios
      .post(`${process.env.REACT_APP_URL_USER}/pdf/get/${user?._id}`)
      .then((res) => {
        setLoading(false);
        setSucees(res.data.message);
        setResponse(true);
        setOpen(true);
      })
      .catch((err) => {
        setLoading(false);
        setError(err.response.data.message);
        setResponse(false);
        setOpen(true);
      });
  };
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  const handleNextCarte = () => {
    setActiveStepsCartedeux((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBackCarte = () => {
    setActiveStepsCartedeux((prevActiveStep) => prevActiveStep - 1);
  };

  const handleResetCarte = () => {
    setActiveStepsCartedeux(0);
  };

  return (
    <div
      style={{
        backgroundImage: `url('./Tori/maestro.jpg')`,
        backgroundPosition: "center",
        backgroundSize: "cover",
        height: "100%",
        width: "100%",
        backgroundColor:
          "rgba(128, 128, 128, 0.5)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
        color: "white",
      }}
    >
      <Box fullwidth>
        <NavLink to="/home">
          <Icon>
            <ArrowBackIcon />
          </Icon>
        </NavLink>

        <Typography
          color="black"
          variant="h6"
          textAlign="center"
          sx={{ textDecoration: "underline" }}
        >
          Le règlement intérieur du groupe
        </Typography>
        <Stepper activeStep={activeStep} orientation="vertical">
          {steps.map((step, index) => (
            <Step key={step.label}>
              <StepLabel
                optional={
                  index === 4 ? (
                    <Typography variant="caption">Last step</Typography>
                  ) : null
                }
              >
                <Typography
                  variant="subtitle1"
                  sx={{ fontSize: "1.2rem", fontWeight: "bolder" }}
                >
                  {" "}
                  {step.label}{" "}
                </Typography>
              </StepLabel>
              <StepContent>
                <Typography variant="subtitle2">{step.description}</Typography>
                <Box sx={{ mb: 2 }}>
                  <div>
                    <Button
                      variant="contained"
                      onClick={handleNext}
                      sx={{ mt: 1, mr: 1 }}
                      size="small"
                    >
                      {index === steps.length - 1 ? "Fin" : "Continue"}
                    </Button>
                    <Button
                      variant="text"
                      disabled={index === 0}
                      onClick={handleBack}
                      sx={{ mt: 1, mr: 1 }}
                    >
                      retour
                    </Button>
                  </div>
                </Box>
              </StepContent>
            </Step>
          ))}
        </Stepper>
        {activeStep === steps.length && (
          <Paper square elevation={0} sx={{ p: 1 }}>
            <Typography>
              Toutes les étapes sont terminées - vous avez terminé
            </Typography>
            <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
              Relire
            </Button>
          </Paper>
        )}
        <br></br>
        {/* <Typography variant='subtitle2'> Ce règlement intérieur peut être modifié à tout moment si cela est nécessaire pour le bon fonctionnement du groupe.</Typography>  */}
        <br></br>
        {loading ? (
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <CircularProgress />
          </Box>
        ) : (
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Button variant="contained" onClick={generatePdf}>
              Recevoir le document par mail
            </Button>
          </Box>
        )}
      </Box>
      <br></br>

      {response ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={7000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
    </div>
  );
}

