import axios from "axios";

import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setGetUser } from "../../../features/user.reducers";
import Navbar from "../../../components/Navbar";
import Card from "./Card";

import { Alert, Skeleton, Snackbar, Stack } from "@mui/material";
import "../../../styles/app.css";
import Header from "../../../components/Header";
import { useNavigate } from "react-router-dom";
axios.defaults.withCredentials = true;

const Home = () => {
  const dispatch = useDispatch();
  const [error, setError] = React.useState("");
  const [open, setOpen] = React.useState(false);

  const history = useNavigate();
  const [res, setResponse] = React.useState(false);
  const user = useSelector((state) => state.user.user);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  /**Au chargement de la page */
  const sendRquest = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };
  useEffect(() => {
    sendRquest()
      .then((data) => {
        dispatch(setGetUser(data.user));
      })
      .catch(() => {
        setTimeout(() => {
          history("/");
        }, 30000);
      });
  }, [dispatch]);

  return (
    <div
    // style={{ backgroundImage: `url('assets/fond1.jpg')`, height: '100%', backgroundPosition: "center", backgroundSize: 'cover', objectFit: 'cover', color: 'white' }}
    >
      <div>
        {user ? (
          <div
            style={{
              backgroundImage: `url('Tori/all39.jpg')`,
              height: "100%",
              backgroundPosition: "center",
              backgroundSize: "cover",
              objectFit: "cover",
              color: "white",
            }}
            // className="background"
          >
            {/* <FixedBottomNavigation /> */}
            <Navbar />
            <br />
            <Header />
            {/* <Typography
        sx={{
         
          fonstSize: "0.7rem",
          fontWeight: "900",
          textAlign: "left",
          marginLeft:"15px"
        }}
        variant="h6"
      >
       Compte FLGP: {user?.lastName} {user?.firstName}
       
      </Typography>  */}
            {/* <Typography sx={{ marginLeft: "15px" }} variant="h6">
        Bienvennue {user?.names}
      </Typography> */}

            {/* <Header /> */}
            {res && (
              <Stack
                spacing={2}
                sx={{
                  marginTop: "50%",
                  transform: "translateY(-50%)",
                  width: "100%",
                }}
              >
                <Snackbar
                  open={open}
                  autoHideDuration={7000}
                  onClose={handleClose}
                >
                  <Alert
                    onClose={handleClose}
                    severity="error"
                    sx={{ width: "100%" }}
                  >
                    {error}
                  </Alert>
                </Snackbar>
              </Stack>
            )}
            <Card />
          </div>
        ) : (
          <>
            <Skeleton variant="text" sx={{ fontSize: "1rem" }} />

            <br />
            <br />

            {/* For other variants, adjust the size with `width` and `height` */}
            <Skeleton variant="circular" width={40} height={40} />
            <br />
            <br />
            <Skeleton variant="rectangular" width="55%" height={40} />
            <br />
            <br />
            <br />
            <Skeleton variant="rounded" width="100%" height={60} />
            <br />
            <br />
            <br />
            <br />
            <Skeleton variant="rounded" width="100%" height={160} />

            <br />
            <br />
            <br />
          </>
        )}
      </div>
    </div>
  );
};

export default Home;
