import React, { useState, useEffect } from "react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import moment from "moment";
import "react-big-calendar/lib/css/react-big-calendar.css";
import { Typography } from "@mui/material";
import QueryBuilderIcon from "@mui/icons-material/QueryBuilder";
import EventIcon from "@mui/icons-material/Event";
import FmdGoodIcon from "@mui/icons-material/FmdGood";
import EventNoteIcon from "@mui/icons-material/EventNote";
import LikeButon from "./LikeButon";
import { useDispatch } from "react-redux";
import { getAllPost } from "../../../features/calendar.reducers";

const localizer = momentLocalizer(moment);

function CalendarComponent() {
  const [events, setEvents] = useState([]);
  // const calendars= useSelector((state) => state.calendars.calendars);
  const dispatch = useDispatch();
  useEffect(() => {
    fetch(`${process.env.REACT_APP_URL_CALENDAR}`)
      // fetch(`${process.env.REACT_URL_CALENDAR}`)
      .then((response) => response.json())
      .then((data) => {
        dispatch(getAllPost(data));
        setEvents(data);

        // console.log(data);
      })
      .catch((error) => console.error(error));
  }, [events, dispatch]);

  const [selectedEvent, setSelectedEvent] = useState(null);

  const handleSelectEvent = (event) => {
    setSelectedEvent(event);
  };

  return (
    <div>
      <h2>Calendrier des évènements du groupe</h2>
      {selectedEvent && (
        <div>
          <Typography align="center" variant="h6">
            {selectedEvent.title}
          </Typography>
          <Typography variant="subtitle2">
            <EventIcon sx={{ color: "#DB9A02" }} /> Le{" "}
            {moment(selectedEvent.start).format("DD/MM/YYYY")}
          </Typography>
          <Typography variant="subtitle2">
            <QueryBuilderIcon sx={{ color: "#DB9A02" }} /> De{" "}
            {moment(selectedEvent.start).format("HH:mm")} -{" "}
            {moment(selectedEvent.end).format("HH:mm")}
          </Typography>
          <Typography variant="subtitle2">
            <FmdGoodIcon sx={{ color: "#DB9A02" }} /> {selectedEvent.location}
          </Typography>
          <Typography variant="subtitle2">
            <EventNoteIcon sx={{ color: "#DB9A02" }} />{" "}
            {selectedEvent.description}
          </Typography>
          <br />

          <LikeButon selectedEvent={selectedEvent} />
          <br />
        </div>
      )}
      <br />
      <Calendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        defaultView="month"
        onSelectEvent={handleSelectEvent}
        style={{ height: "500px", backgroundColor: "#DB9A02" }}
      />
    </div>
  );
}

export default CalendarComponent;
