import React, { useState, useEffect } from "react";
import axios from "axios";
import Navbar from "../NavbarAdmin";

function Calendar() {
  const [calendarData, setCalendarData] = useState([]);
  const [likersData, setLikersData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const result = await axios.get(`${process.env.REACT_APP_URL_CALENDAR}`);
      setCalendarData(result.data);
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchLikersData = async () => {
      const likersData = await Promise.all(
        calendarData.map((event) => {
          return Promise.all(
            event.likers.map(async (liker) => {
              const user = await fetchUserData(liker);
              return user;
            })
          );
        })
      );
      setLikersData(likersData);
    };
    fetchLikersData();
  }, [calendarData]);

  const fetchUserData = async (userId) => {
    const result = await axios.get(
      `${process.env.REACT_APP_URL_USER}/${userId}`
    );
    // console.log(result.data.message);
    return result.data.message;
  };

  return (
    <div>
      <Navbar />
      <h1>Calendar</h1>
      <ul>
        {calendarData.map((event, index) => (
          <li key={event._id}>
            <h2>{event.title}</h2>
            {/* <p>{event.description}</p> */}
            <p>Participants:</p>
            <ul>
              {event.likers.length}
              {likersData[index] &&
                likersData[index].map((user) => (
                  <li key={user._id}>
                    Nom: {user.names} - Tel: {user.tel} - Partition :{" "}
                    {user.partition}
                  </li>
                ))}
            </ul>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Calendar;
