import { BrowserRouter, Route, Routes } from "react-router-dom";
import Home from "./pages/accueil/home/Home";
import HomeAdmin from "./pages/admin/Home";
import SignInSide from "./pages/connexion/SignIn";
import SignUpSide from "./pages/connexion/SignUp";
import { ThemeProvider } from "@mui/system";
import theme from "./utils/theme";
import Partition from "./pages/accueil/partition/Partition";
import CartePeronnel from "./pages/accueil/card/CartePeronnel";
import Profil from "./pages/profil/Profil";
import Forget from "./pages/connexion/Forget";
import Reset from "./pages/connexion/Reset";
import Notification from "./pages/notification/Notification";
import Education from "./pages/accueil/education/Education";
import Validation from "./pages/accueil/card/Validation";
import { setGetUser } from "./features/user.reducers";
import { useDispatch } from "react-redux";
import axios from "axios";
import { useEffect } from "react";
import Transaction from "./pages/accueil/transaction/Transaction";

import CardUsers from "./pages/admin/users/CardUsers";
import Validate from "./pages/admin/validation/Validate";
import Parent from "./pages/admin/presence/Parent";
import ParentEvaluation from "./pages/admin/evaluation/ParentEvaluation";
import Politique from "./pages/connexion/Politique";
import PartitionImage from "./pages/accueil/partition/PartitionImage";
import Condition from "./pages/connexion/Condition";
import Souscription from "./pages/accueil/education/Souscription";
import Morceau from "./pages/admin/morceau/Moreceau";
import DataImage from "./pages/admin/image/DataImage";
import Participant from "./pages/admin/participants/Participant";
import HomeGallery from "./pages/gallery/HomeGalery";
import AudioPlayer from "./pages/audio/MusicPlayer";
import VideoPlayer from "./pages/video/VideoPlayer";
import HomeExercices from "./pages/exercices/HomeExercices";
import Test from "./pages/Test";
import "./styles/app.css";

const App = () => {
  const dispatch = useDispatch();
  const sendRquest = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };

  useEffect(() => {
    sendRquest().then((data) => {
      dispatch(setGetUser(data.user));
    });
  }, [dispatch]);
  return (
    <div>
      <ThemeProvider theme={theme}>
        <BrowserRouter>
          <Routes>
            <Route path="/*" element={<SignInSide />} />
            {/* Connexion */}
            <Route path="/register" element={<SignUpSide />} />
            <Route path="/test" element={<Test />} />
            <Route path="/img" element={<DataImage />} />
            <Route path="/login" element={<SignInSide />} />
            <Route path="/home" element={<Home />} />
            <Route path="/partition" element={<Partition />} />
            <Route path="/partitions/:id" element={<PartitionImage />} />
            <Route path="/carte" element={<CartePeronnel />} />
            <Route path="/profil" element={<Profil />} />
            <Route path="/forget" element={<Forget />} />
            <Route path="/reset/:token" element={<Reset />} />
            <Route path="/notification" element={<Notification />} />{" "}
            <Route path="/education" element={<Education />} />
            <Route path="/validation" element={<Validation />} />
            <Route path="/transaction" element={<Transaction />} />
            <Route path="/politique" element={<Politique />} />
            <Route path="/condition" element={<Condition />} />
            <Route path="/souscription" element={<Souscription />} />
            <Route path="/gallery" element={<HomeGallery />} />
            <Route path="/audio" element={<AudioPlayer />} />
            <Route path="/video" element={<VideoPlayer />} />
            <Route path="/exercices" element={<HomeExercices />} />
            {/* Admin Route  */}
            <Route path="/admin" element={<HomeAdmin />} />
            <Route path="/admin/home-morceau" element={<Morceau />} />
            <Route path="/admin/home-users" element={<CardUsers />} />
            <Route path="/admin/home-participants" element={<Participant />} />
            <Route path="/admin/home-validate" element={<Validate />} />
            <Route path="/admin/home-liste" element={<Parent />} />
            <Route path="/admin/home-note" element={<ParentEvaluation />} />
          </Routes>
        </BrowserRouter>
      </ThemeProvider>
    </div>
  );
};

export default App;
